.section-hero-about {
    position: relative;
    padding-top: 6.5rem;
    margin-top:-4rem;
    padding-bottom: 5rem;
    overflow-x: clip;
    overflow-y: visible;
    background-color: $primary-blue;
    @include media-breakpoint-up(lg) {
        padding-top: 9.5rem;
        margin-top:-6rem;
    }

    h1 {
        text-align: center;
        position: relative;

        br {
            @include media-breakpoint-down(md) {
                display: none;
            }
        }

        span {
            @include media-breakpoint-down(md) {
                font-size: toRem(42);
            }
        }
    }

    img {
        height: auto;
        max-width: toRem(235);
        opacity: 0.5;
        transition: opacity 0.4s ease, width 0.4s ease;
        width: 100%;
    }

    .hero-about-gallery-holder {
        align-items: center;
        display: flex;
        height: 12rem;
        justify-content: center;
        position: relative;
        opacity: hidden;
        @include media-breakpoint-up(md) {
            height: 26rem;
        }
        @include media-breakpoint-up(xl) {
            height: 22rem;
        }

        .hero-about-gallery {
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            
            @include media-breakpoint-down(md) {
                min-height: toRem(325);
                position: relative;
                transform: translateY(6rem);
                width: 100%;
                max-width: 380px;
            }

            .gallery-image-slide {
                background: $primary-blue;
                @include media-breakpoint-down(md) {
                    position: absolute;
                }

                &:nth-child(1) {
                    img {
                        width: toRem(142);
                    }

                    @include media-breakpoint-down(md) {
                        top: 0;
                        left: 49%;
                        transform: translateX(-50%);

                        img {
                            opacity: 1;
                            width: toRem(140);
                        }
                    }
                }

                &:nth-child(2) {
                    img {
                        width: toRem(173);
                    }

                    @include media-breakpoint-down(md) {
                        left: 5%;
                        top: toRem(28);

                        img {
                            opacity: 0.5;
                            width: toRem(81);
                        }
                    }
                }

                &:nth-child(3) {
                    img {
                        width: toRem(235);
                        opacity: 1;
                    }

                    @include media-breakpoint-down(md) {
                        left: toRem(54);
                        top: toRem(150);

                        img {
                            width: toRem(45);
                            opacity: 0.5;
                        }
                    }
                }

                &:nth-child(4) {
                    img {
                        width: toRem(133);
                    }

                    @include media-breakpoint-down(md) {
                        right: 5%;
                        top: toRem(15);

                        img {
                            opacity: 0.5;
                            width: toRem(88);
                        }
                    }

                }

                &:nth-child(5) {
                    img {
                        opacity: 1;
                        width: toRem(207);
                    }

                    @include media-breakpoint-down(md) {
                        right: 11%;
                        top: 8.625rem;

                        img {
                            opacity: 0.5;
                            width: toRem(64);
                        }
                    }
                }

                &:nth-child(6) {
                    img {
                        width: toRem(142);
                    }

                    @include media-breakpoint-down(md) {
                        display: none;
                    }
                }

                &:nth-child(7) {
                    img {
                        width: toRem(276);
                    }

                    @include media-breakpoint-down(md) {
                        display: none;
                    }
                }

                &:nth-child(8) {
                    img {
                        width: toRem(160);
                    }

                    @include media-breakpoint-down(md) {
                        display: none;
                    }
                }

                &:nth-child(9) {
                    img {
                        width: toRem(116);
                    }

                    @include media-breakpoint-down(md) {
                        display: none;
                    }
                }
            }
        }
    }
}