.two-column-panels {
    padding-top: 1.25rem;
    
    &__block {
        position: relative;
    }

    .content-wrap.has-link {
        &:hover {
            .two-column-panels__header{
                text-decoration: underline rgba($secondary-gold,  1) 3px;
            }
        }
    }

    &__link {
        text-decoration: none;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }

    &__header {
        text-decoration: underline rgba($secondary-gold,  0) 3px;
        transition: text-decoration-color 0.3s;
        // font-family: $EphemeraEgyptianMedium;
        font-size: toRem(36);
        @include media-breakpoint-up(md) {
            font-size: toRem(42);
        }
    }

    &__image {
        width: 100%;
        height: 220px;
        object-fit: cover;
        @include media-breakpoint-up(md) {
            height: 330px;
        }
        @include media-breakpoint-up(lg) {
            height: 430px;
        }
    }

    &__cta {
        z-index: 1;
    }

    &:not(.design-mode){
        .two-column-panels__block{
            opacity: 0;
            transform: translateY(40px);
            transition: all 0.5s;
            
            &.gsap-on {
                opacity: 1;
                transform: translateY(0);
            }            
        }
    }

    &.theme--dark{
        background-color: $primary-blue;
        color:$white;
    }

    &.theme--light{
        background-color: $primary-light-grey;
        .two-column-panels__block{
            .header, .sub_header{
                color:$primary-blue;
            }
        }
    }

}