.accordion {
    background: none;
    
    .accordion-item {
        color: $primary-blue;
        background: none;
        border-bottom: 1px solid $primary-blue;
        &:last-child {
            border-bottom: 1px solid $primary-blue;
        }
    
        &:has(.accordion-collapse.collapse.show) {
            .icon-holder {
                transform: rotate(270deg);
            }
        }
    
        .accordion-header {
            padding: 1.75rem 0;
            @include media-breakpoint-up(lg) {
                padding: 3rem 0 2.5rem;
            }

            .accordion-button {
                background: none;
                box-shadow: none;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 0;
    
                &::after {
                    content: none;
                }
    
                &__header {
                    @include header-2;
                    color: $primary-blue;
                    margin-bottom: 0;
                }
    
                .icon-holder {
                    transform: rotate(90deg);
                    transform-origin: center;
                    transition: transform 0.3s;
                }
            }
        }
    
        .accordion-body {
            color: $primary-blue;
            @include body-2;
            padding: 0;
            @include media-breakpoint-up(lg) {
                margin-bottom: 3rem;
            }

            &.rte {
                margin-bottom: 0;
                padding-right: 1rem;
            }
    
            h2 { 
                @include body-1;
                margin-top: 0;
                margin-bottom: 0.375rem;
            }
    
            p { 
                @include body-2;
                margin-bottom: 2rem;
            }

        }
    }
}
