// Colors
$black: #000;
$primary-blue: #14315C;
$primary-light-grey: #F2F2F2;
$primary-dark-grey: #EAE8E8;
$gray-dark: #DCDDDE;
$secondary-white: #FFFFFF;
$secondary-blue: #316E9E;
$secondary-gold: #B88B20;
$tertiary-gold: #5A3F00;

$theme-colors: (
    "primary" : $primary-blue,
    "white": $secondary-white,
    "black": $black,
    "blue": $primary-blue,
    "gray-light": $primary-light-grey,
    "gray-dark": $primary-dark-grey,
    "blue-secondary": $secondary-blue,
    "gold": $secondary-gold,
    "gold-dark": $tertiary-gold,
);

$grid-breakpoints: (
    xs: 0,
    sm: 576px,
    md: 768px,
    lg: 992px,
    xl: 1200px,
    xxl: 1400px
);

$container-max-widths: (
    xs: 90%,
    sm: 540px,
    md: 720px,
    lg: 960px,
    xl: 1140px,
    xxl: 1320px
);