.main-header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    
    #search-desktop {
        display: none;
        @include media-breakpoint-up(lg) {
            &.show {
                display: block;
            }
        }

        .search-bar {
            padding-top: 2.5rem;
            padding-bottom: 5rem;
        }

        .search-overlay {
            background: $primary-blue;
            width: 100%;
            height: 100vh;
            opacity: 0.8;
            cursor: pointer;
        }
    }
}

.navbar {
    padding: 0.7rem 0.875rem 1.1rem;
    --bs-navbar-brand-margin-end: 0;
    transition-duration: 0.3s;
    .navbar-logo{
        margin-top:-3px;
        margin-left:10px;
    }
    @include media-breakpoint-up(lg) {
        padding: 1.9rem .875rem 1.8rem;
        .navbar-logo{
            margin-top:-5px;
        }
    }

 

    &.navbar-dark {
        background: $primary-blue;
        .nav-link, .depth-0.link a { color: $primary-blue; }
        @include media-breakpoint-up(lg) {
            .logo-white { fill: $secondary-white; }
            .nav-link, .depth-0.link a { color: $secondary-white; }
            .depth-0.link::before { background-color: $secondary-white; }
            .dropdown-toggle {
                &::before { background-color: $secondary-white; }
                svg > path { fill: $secondary-white; }
            }
            .nav-icon {
                svg > path {
                    fill: $secondary-white;
                    transition-duration: 0.3s;
                }
            }
        }
    }

    &.navbar-light {
        background: $primary-light-grey;
        .nav-link, .depth-0.link a { color: $primary-blue; }
        .logo-white { fill: $primary-blue; }
        #navOpen, #navCart {
            background: $primary-blue;
            line {
                stroke: $secondary-white;
            }
            path{
                fill: $secondary-white;
            }
        }
        @include media-breakpoint-up(lg) {
            .nav-link, .depth-0.link a { color: $primary-blue; }
            .depth-0.link::before { background-color: $primary-blue; }
            .dropdown-toggle {
                &::before { background-color: $primary-blue; }
                svg > path { fill: $primary-blue; }
            }
    
            .navbar-toggler {
                background: $primary-blue;
                line { stroke: $secondary-white; }   
            }
            
            .navbar-utility {
                .nav-icon {
                    svg > path {
                        fill: $primary-blue;
                        transition-duration: 0.3s;
                    }
                    &:hover {
                        svg > path {
                            fill: $secondary-gold;
                        }
                    }
                }
            }
        }
    }

    &.transparent {
        background: transparent;
        @include media-breakpoint-up(lg) {
            .logo-white { fill: $secondary-white; }
            .nav-link, .depth-0.link a { color: $secondary-white; }
            .depth-0.link::before { background-color: $secondary-white; }
            .dropdown-toggle {
                &::before { background-color: $secondary-white; }
                svg > path { fill: $secondary-white; }
            }
            .nav-icon {
                svg > path {
                    fill: $secondary-white;
                    transition-duration: 0.3s;
                }
            }
            &.navbar-light {

                .logo-white { fill: $secondary-white; }
                .nav-link, .depth-0.link a { color: $secondary-white; }
                .depth-0.link::before { background-color: $secondary-white; }
                .dropdown-toggle {
                    &::before { background-color: $secondary-white; }
                    svg > path { fill: $secondary-white; }
                }
                .nav-icon {
                    svg > path {
                        fill: $secondary-white;
                        transition-duration: 0.3s;
                    }
                }
            }

        }
        &.navbar-light {
            .logo-white { fill: $secondary-white; }
            #navOpen, #navCart {
                background: $secondary-white;
                line {
                    stroke: $primary-blue;
                }
                path {
                    fill: $primary-blue;
                }
            }
        }

        &.show {
            &.navbar-dark {
                background: $primary-blue;
            }
            &.navbar-light {
                background: $primary-light-grey;
                .nav-link, .depth-0.link a { color: $primary-blue; }
                .logo-white { fill: $primary-blue; }
                #navOpen, #navCart {
                    background: $primary-blue;
                    line {
                        stroke: $secondary-white;
                    }
                    path{
                        fill: $secondary-white;
                    }
                }
                @include media-breakpoint-up(lg) {
                    .nav-link, .depth-0.link a { color: $primary-blue; }
                    .depth-0.link::before { background-color: $primary-blue; }
                    .dropdown-toggle {
                        &::before { background-color: $primary-blue; }
                        svg > path { fill: $primary-blue; }
                    }
            
                    .navbar-toggler {
                        background: $primary-blue;
                        line { stroke: $secondary-white; }   
                    }
                    
                    .navbar-utility {
                        .nav-icon {
                            svg > path {
                                fill: $primary-blue;
                                transition-duration: 0.3s;
                            }
                            &:hover {
                                svg > path {
                                    fill: $secondary-gold;
                                }
                            }
                        }
                    }
                }
            }
        

        }
    }



    &-logo {
        width: 4.5rem;
        height: auto;
        z-index: 2;
        @include media-breakpoint-up(lg) {
            width: 4.75rem;
            height: auto;
            z-index: 3;
            margin-top: -3px;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &-toggler {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: absolute;
        right: 1rem;
        top: 1rem;
        width: 2.75rem;
        height: 2.75rem;
        background: $secondary-white;
        border: none;
        border-radius: 50%;

        &--line {
            width: 0.875rem;
            height: 1px;
            margin: 1.25px 0;
            background: $primary-blue;
        }

        svg {
            width: 1rem;
            height: 0.75rem;
        }
    }

    #navCart {
        position: absolute;
        background: $primary-blue;
        right: 4.5rem;
        box-shadow: none;

        svg {
            width: 1.4rem;
            height: 100%;
        }
    }

    #navClose {
        background: $primary-blue;
        box-shadow: none;
        right: 1rem;

        svg {
            width: 1.1rem;
            height: 100%;
        }
    }

    .navbar-utility {
        z-index: 3;
        position: absolute;
        right: 1.875rem;
        height: 100%;

        .nav-cart {
            position: relative;
            svg {
                width: 1.5rem;
            }

            .clickable {
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                &:focus {
                    outline: -webkit-focus-ring-color auto 1px;
                    outline-offset: 1px;
                }
            }

            .cart-count {
                position: absolute;
                display: flex;
                justify-content: center;
                align-items: center;
                left: 1rem;
                top: -0.2rem;
                width: 1rem;
                height: 1rem;
                font-family: $SaaSeries;
                font-size: 0.6rem;
                text-decoration: none;
                border-radius: 50%;
                background: $primary-blue;
                color: $white;
            }
        }

        .nav-search {
            margin-left: 2.25rem;
            cursor: pointer;
            @include media-breakpoint-up(lg) {
                height: 100%;
                display: flex;
                align-items: center;
                padding: 0 1.25rem;
                margin: 0 1.25rem;

                &.show {
                    background: $primary-light-grey;
                    svg > path {
                        fill: $primary-blue;
                    }
                }
            }

            svg {
                width: 1rem;
            }
        }

        .nav-pin {
            margin-left: 2.25rem;
            cursor: pointer;
            @include media-breakpoint-up(lg) {
                margin: 0 auto;
            }

            svg { width: 0.75rem; }
        }

        .nav-icon {
            svg > path {
                fill: $secondary-white;
                transition-duration: 0.3s;
            }
            &:hover {
                svg > path { fill: $secondary-gold; }
            }
        }
    }

    &-collapse {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        z-index: 3;
        background: $primary-light-grey;
        @include media-breakpoint-up(lg) {
            height: auto;
            background: transparent;
            color: $secondary-white;
            padding-left: 4.25rem;
            z-index: 2;
        }


        &__header {
            position: relative;
            
            .menu-header {
                width: fit-content;
                color: $primary-blue;
                opacity: 1;
                margin-left: 0;
                transition: margin-left 0.3s;

                &.hide {
                    margin-left: 3rem;
                }
            }

            .menu-back {
                position: absolute;
                left: -0.5rem;
                border: none;
                background: none;
                opacity: 0;
                transition: opacity 0.3s;

                svg {
                    width: 2.75rem;
                    height: 2.75rem;
                    margin-right: 0.375rem;
                }

                p {
                    font-family: $SaaSeries;
                    font-size: 0.875rem;
                    text-transform: uppercase;
                }

                &.show {
                    opacity: 1;
                }
            }

            .navbar-toggler {
                padding: 0.625rem;
                box-shadow: 0 15px 30px 0 rgba(74, 1, 18, 0.25);
                position: relative;
                right: inherit;
                top: inherit;

                &:focus {
                    border-color: #86b7fe;
                    box-shadow: 0 0 0 0.25rem #0d6efd40;
                }
            }
        }

        // Subnav Menus
        .subnav {
            position: absolute;
            max-width: 100%;
            width: 100%;
            height: 100%;
            background: $primary-light-grey;
            left: 100%;
            top: 4.75rem;
            // padding-left: 1.25rem;
            // padding-right: 1.25rem;
            transition: left 0.3s;
            z-index: 2;
            color: $primary-blue;

            &__title {
                // border-bottom: 1px solid $primary-blue;
                // margin-left: 0.75rem;
                margin-bottom: 1.5rem;
                padding-bottom: 0.75rem;
                // padding-left: 0;
            }

            &__link {
                padding-left: 0;
                list-style: none;
                font-family: $SaaSeries;
                font-size: 1.125rem;
                margin-bottom: 1.75rem;

                a {
                    color: $primary-blue;
                    text-decoration: none;

                    svg {
                        transform-origin: center;
                        margin-left: 0.375rem;
                        transform: rotate(-90deg);
                        width: 12px;
                        height: 9px;
                    }
                }
                
            }

            &.show {
                left: 0;
            }
        }
    }

    &-nav {
        margin-top: 1.875rem;
        padding-right: calc(var(--bs-gutter-x) * .5);
        padding-left: calc(var(--bs-gutter-x) * .5);
        @include media-breakpoint-up(lg) {
            margin-top: 3.375rem;
            margin-left: 2.5rem;
            padding-left: 2.5rem;
        }

        .row {
            margin: 0 auto;
            width: 100%;
        }

        .dropdown {
            @include media-breakpoint-up(lg) {
                position: inherit;
            }
            
            &-toggle {
                padding: 0 0 2.5rem 0;
                @include media-breakpoint-up(lg) {
                    color: $secondary-white;
                    margin-right: 2.5rem;
                    font-size: 0.875rem;
                    position: relative;
                }

                @include media-breakpoint-up(xl) {
                    margin-right: 3.5rem;
                }

                &::after {
                    display: none;
                }

                &::before {
                    display: none;
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: -10px;
                    width: 100%;
                    height: 10px;
                    background: $primary-light-grey;
                    transform: scaleX(0);
                    opacity: 0;
                    transition: all 0.3s;
                    @include media-breakpoint-up(lg) {
                        display: block;
                    }
                }

                &.show {
                    &::before {
                        opacity: 1;
                        transform: scaleX(1);
                    }
                }

                &:hover {
                    &::before {
                        opacity: 1;
                        transform: scaleX(1);
                    }
                }

                svg {
                    transform-origin: center;
                    transform: rotate(-90deg);
                    margin-left: 0.25rem;
                    @include media-breakpoint-up(lg) {
                        transform: rotate(0);
                        margin-left: 0.325rem;
                        width: 0.75rem;
                        height: 0.625rem;

                        path {
                            fill: $secondary-white;
                        }
                    }
                }
            }

            &-menu {
                @include media-breakpoint-down(lg) {
                    display: none;
                }
                @include media-breakpoint-up(lg) {
                    width: 100%;
                    border-radius: 0;
                    border: none;
                    padding-left: 9.5rem;
                    padding-top: 3.25rem;
                    padding-bottom: 1.75rem;
                    top: 7.3rem;
                }   
                
                &.show {
                    @include media-breakpoint-up(lg) {
                        display: flex;
                        flex-wrap: wrap;
                        background: rgba(242, 242, 242, 0.95);
                    }
                }
                
                .link {
                    @include media-breakpoint-up(lg) {
                        width: 22%;
                    }
                    a {
                        @include media-breakpoint-up(lg) {
                            font-family: $SaaSeries;
                            font-size: 1.25rem;
                            color: $primary-blue;
                            text-decoration: none;
                            cursor: default;
                        }
                        
                        p {
                            width: fit-content;
                            transition-duration: 0.2s;
                            cursor: pointer;
                            @include media-breakpoint-up(lg) {
                                &:hover {
                                    color: $secondary-gold;
                                }
                            }
                        }
                    }

                    &.product {
                        width: 18%;
                        margin-right: 2%;
                        max-width: 200px;
                        margin-top: 1.25rem;
                        &:last-of-type {
                            margin-right: 0;
                        }
                        
                        .product-image {
                            width: 100%;
                            height: 140px;
                            overflow: hidden;

                            img {
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                                transform: scale(1);
                                transition-duration: 0.4s;
                            }

                            &.image-contain > img {
                                object-fit: contain;
                            }
                        }

                        a {
                            cursor: pointer;
                        }

                        @include media-breakpoint-up(lg) {
                            &:hover {
                                a > p {
                                    color: $secondary-gold;
                                }
                                .product-image {
                                    img {
                                        transform: scale(1.2);
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        .depth-0.link {
            padding: 0 0 2.5rem;
            position: relative;
            cursor: pointer;
            line-height: 0;
            
            @include media-breakpoint-up(lg) {
                margin-right: 2.5rem;
            }

            @include media-breakpoint-up(xl) {
                margin-right: 3.5rem;
            }

            &::before {
                content: "";
                position: absolute;
                left: 0;
                bottom: -10px;
                width: 100%;
                height: 10px;
                transform: scaleX(0);
                opacity: 0;
                transition: all 0.3s;
            }

            &:hover {
                &::before {
                    opacity: 1;
                    transform: scaleX(1);
                }
            }
            
            a {
                color: $primary-blue;
                text-decoration: none;
                @include media-breakpoint-up(lg) {
                    padding-right: var(--bs-navbar-nav-link-padding-x);
                    padding-left: var(--bs-navbar-nav-link-padding-x);
                    color: $secondary-white;
                }
            }
        }

    }

}

.navbar-collapse {
    @include media-breakpoint-up(lg) {
        background: none;
    }

    .navbar-nav {
        &-ul{
            list-style:none;
            @include media-breakpoint-up( lg ){
                display:inherit;
            }
        }
        li, li > a {
            line-height: 1;
            @include media-breakpoint-up(lg) {
                width: fit-content;
            }
        }
    }
}

