.shopify-section {
    &:has(.sticky-nav) {
        position: sticky;
        bottom: 0;
        z-index: 1;
    }
}

.sticky-nav {
    background-color: $primary-blue;
    color:$secondary-white;
    z-index: 999;
    width: 100%;
    height: 60px;    

    @include media-breakpoint-down(md) {
        display: none;
        pointer-events: none;
        visibility: hidden;
        
    }
    
    ul {
        display:flex;
        justify-content: space-evenly;
        align-items: center;
        height: 100%;
        text-align: center;
        padding-left: 0;
        li{
            display: inline-flex;
            list-style: none;

        }
        a{
            color:$secondary-white;
            text-decoration: underline;
        }
    }

}