.product-section-related-content {
    padding: 1rem 0 7rem;
    @include media-breakpoint-up(lg) {
        padding-bottom: 6rem;
    }
    
    .swiper {
        @include media-breakpoint-up(lg) {
            padding-right: calc(var(--bs-gutter-x) * .5);
        }
    }

    .related-content-swiper {
        @include media-breakpoint-up(lg) {
            overflow: hidden;
        }
    }

    .card-basic {
        @include media-breakpoint-up(md) {
            width: 270px;
        }
        .content {
            width: 100%;
        }

        .image-wrap {
            @include media-breakpoint-up(lg) {
                width: 100%;
            }   
        }
    }
}