.rte {

    h2, h3 {
        margin-top: 3.5rem;
        margin-bottom: 2rem;
        & + ul {
            margin-top: -1.5rem;
        }
    }

    // h2 {
    //     @include header-2;
    // }

    ul {
        padding-left: 0;
        list-style: none;
        text-align: left;

        li {
            font-family: $ITCClearface;
            position: relative;
            padding: 2rem 0 2rem 2rem;
            border-bottom: 1px solid $primary-blue;

            &::before {
                content: "";
                position: absolute;
                width: 0.375rem;
                height: 0.375rem;
                left: 0;
                top: 2.45rem;
                background: $primary-blue;
                border-radius: 50%;
            }

            &:last-of-type {
                border-bottom: none;
            }
        }
    }

    a {
        color: $secondary-gold;
        overflow-wrap: anywhere;
    }

    img {
        height: auto;
        max-width: 100%;
        object-fit: cover;
        margin: 2rem auto;
    }

    .content, p {
        @include body-2;
    }

    blockquote {
        position: relative;
        padding-top: 2rem;
        margin-top: 3rem;

        &::before, &::after {
            content: '';
            background: url('https://cdn.shopify.com/s/files/1/0688/3596/7262/files/quotes.svg?v=1682971862') no-repeat;
            background-size: contain;
            position: absolute;
            width: 2rem;
            height: 2rem;
            top: 0;
            left: 41%;
            transform: translateX(-50%);
            @include media-breakpoint-up(md) {
                left: 45%;
            }
            @include media-breakpoint-up(lg) {
                left: 44%;
            }
        }

        &::after {
            transform: scale(-1)translate(-50%);
            left: 47%;
            top: -0.5rem;
            @include media-breakpoint-up(md) {
                left: 51%;
            }
            @include media-breakpoint-up(lg) {
                left: 49%;
            }
        }

        p {
            @include body-1;
            text-align: center;
            margin-bottom: 2.5rem;
            margin-top: 2rem;
        }

        @include media-breakpoint-up(lg) {
            max-width: 60ch;
        }
    }

    h4 { 
        @include header-4; 
        text-align: center;
    }
    h5 { 
        @include header-5; 
        text-align: center;
    }
    h6 { 
        font-family: $SaaSeries;
        font-size: toRem(22);
        line-height: 1.2;
        margin-top: 3rem;
        margin-bottom: 1rem;
        @include media-breakpoint-up(md) {
            margin-top: 7rem;
            margin-bottom: 2rem;
        }
    }

    table{
        width: 100%;
        tr td{
            vertical-align: top;
            padding:.75rem;
        }
        ul{
            padding: revert;
            li{
                border-bottom: 0px;
                padding: revert;
                position: relative;        
                margin-bottom:.5rem;
                &:after,&:before{
                    display: none;
                }
                list-style: initial;
            }
        }

        thead, tbody, tfoot, tr, td, th {
            border-color: $primary-blue;
            border-style: dashed;
            border-width: 1px;
        }
    }

}