.product-section-details {
    position: relative;
    background: $primary-light-grey;
    color: $primary-blue;
    overflow: hidden;
    padding-bottom: 7rem;

    .section-image {
        margin-bottom: 2rem;
        position: relative;
        z-index: 0;
        @include media-breakpoint-up(lg) {
            margin-bottom: 0;
        }

        img {
            width: 100%;
            height: auto;
            max-height: 380px;
            object-fit: cover;
            @include media-breakpoint-up(lg) {
                height: auto;
                max-height: 650px;
            }
        }
    }
}

.modal-nfp {
    img {
        object-fit: contain;
        width: 100%;
        height: 100%;
    }

    .modal-dialog {
        top: 5vh;
        @include media-breakpoint-up(md) {
            top: 4%;
            max-width: 80%;
        }
        @include media-breakpoint-up(lg) {
            top: 2rem;
            max-width: 470px;
        }
        @include media-breakpoint-up(xxl) {
            max-width: 620px;
        }
    }

    .modal-content {
        background: $secondary-white;
    }
}