.section-stacking-cards {
    padding: toRem(12) 0 toRem(200);
    margin-bottom: toRem(100);
    position: relative;

    .card-content {
        height: 100%;

        .btn-primary {
            display: flex;
            justify-content: center;
            align-self: flex-start;
            margin-top: 1rem;

            @include media-breakpoint-down(md) {
                padding: .65rem 1rem .575rem;
            }
        }
    }

    .card {
        background: $secondary-white;
        border-radius: 0;
        border: none;
        margin: toRem(50) auto;
        min-width: toRem(318);
        height: clamp(75vh, 2.5vw, 80vh);
        overflow: hidden;
        width: 96vw;
        min-height: 550px;
        transform-origin: 50% 0;
        overflow-y: visible;
        overflow-x: clip;
        box-shadow: 0 -30px 60px rgba(0, 0, 0, 0.16);
        @include media-breakpoint-down(md) {
            max-width: 330px !important;
        }
        @include media-breakpoint-up(lg) {
            height: 650px;
            width: 100%;
        }

        .card-image {
            overflow: hidden;
            position: relative;

            img {
                left: 0;
                top: 0;
                object-fit: cover;
                position: absolute;
                height: 100%;
                width: 100%;

                @include media-breakpoint-down(md) {
                    position: relative;
                }
            }
        }


        h1,
        p {
            color: $primary-blue;
        }

        h1 {

            @include media-breakpoint-down(md) {
                font-size: max(3rem, min(0.375rem + 8.125vw, 8.875rem));
                margin-bottom: 0;
            }
        }

        p {
            @include body-1;

            @include media-breakpoint-down(md) {
                font-size: 1rem;
            }
        }

        .card-image {
            img {
                @include media-breakpoint-down(md) {
                    max-height: toRem(250);
                    height: 30vh;
                }
            }
        }

        .card-details {
            display: flex;
            flex-direction: column;
            flex-grow: 1;
            background-color: $secondary-white;
            // padding: max(5.5rem, min(2.5vw, 7.1875rem)) max(2.25rem, min(2.5vw, 3.375rem)) max(2.3rem, min(2.5vw, 6.75rem)) max(4.6rem, min(2.5vw, 41.6875rem));
            padding: max(0.8rem, min(2.5vw, 5.1875rem)) max(1.25rem, min(2.5vw, 3.375rem)) max(1.3rem, min(2.5vw, 6.75rem)) max(1.25rem, min(2.5vw, 41.6875rem));

            @include media-breakpoint-down(md) {
                min-height: 22rem;
            }
        }
    }

    .bricks {
        width: 100%;
        object-fit: contain;
        position: absolute;
        bottom: -3.5rem;
        right: 0;
        z-index: -1;
        opacity: 0.5;
    }    
}