.nav-cart.empty .count {
	display: none;
  }
  
  .product-form {
	.price {
	  // font-size: 2em;
	  // font-family: "Barlow Condensed", sans-serif;
	}
  
	.price__sale {
	  display: none;
	}
  
	.price--on-sale {
	  .price__sale {
		display: block;
		.price-item--regular{
		  text-decoration: line-through;
		  opacity: .5;
		}
	  }
  
	  .price__regular {
		display: none;
		font-size: 1em;
	  }
	}
  }
  
  
  
  
  
  .clickable{
	cursor:pointer;
  }
  
  /*---- 
  Utilities 
  --- */
//   .no-js {
// 	display: none;
//   }
  
  
  /*---- 
  Product Prices 
  --- */
  .price {
	.price__sale {
	  display: none;
	}
  
	.price__unit {
	  display: none;
	}
  
	.price__badge--sale {
	  display: none;
	}
  
	.price__badge--sold-out {
	  display: none;
	}
  
	.price__badge--sold-out {
	  display: none;
	}
  
	&.price--unavailable {
	  .price__badge--sale {
		display: none;
	  }
  
	  .price__badge--sold-out {
		display: block;
	  }
	}
  }
  
  /*---- 
  Product  
  --- */
  .product-form__error-message-wrapper {
	display: none;
  }
  
  [sh-add-to-cart][aria-disabled="true"] {
	pointer-events: none;
  }
  
  /*---- 
  Nav Cart
  --- */
  [sh-nav-cart].empty [sh-count] {
	display: none;
  }
  
  /*---- 
  Cart Page
  --- */
  [sh-cart-wrapper] {
	.cart {
	  display: block;
	}
  
	.empty-page-content {
	  display: none;
	}
  
	&.empty {
	  .cart {
		display: none;
	  }
  
	  .empty-page-content {
		display: block;
	  }
	}
  }
  
  
  /*---- 
  Temporary Layout  
  --- */
  .flex-row-center {
	display: flex;
	justify-content: center;
	align-items: center;
  }
  
  
  .animation-close {
	position: relative;
	margin: auto;
	width: 24px;
	height: 24px;
	cursor: pointer;
	display: block;
  
	.leftright {
	  height: 2px;
	  width: 24px;
	  position: absolute;
	  top: 12px;
	  left: 0;
	  background-color: #212529;
	  border-radius: 2px;
	  transform: rotate(45deg);
	  transition: all .3s ease-in;
	}
  
	.rightleft {
	  height: 2px;
	  width: 24px;
	  position: absolute;
	  top: 12px;
	  left: 0;
	  background-color: #212529;
	  border-radius: 2px;
	  transform: rotate(-45deg);
	  transition: all .3s ease-in;
	}
  
  
	&:hover .leftright {
	  transform: rotate(-45deg);
	  background-color: #bb1a24
	}
  
	&:hover .rightleft {
	  transform: rotate(45deg);
	  background-color: #bb1a24
	}
  
  }