.header-content {
    position: relative;
    padding-top: 3rem;
    padding-bottom: 1rem;
    @include media-breakpoint-up(lg) {
        padding-top: 5.5rem;
        padding-bottom: 2rem;
    }

    &:not(.design-mode){
        .header-content__gsap {
            opacity: 0;
            transform: translateY(30px);
            transition: all 0.5s;
            @include media-breakpoint-up(lg) {
                transform: translateY(50px);
            }
            &.gsap-on {
                opacity: 1;
                transform: translateY(0);
            }
        }
    }

    .container {
        position: relative;
        z-index: 1;
    }

    &.theme--dark {
        background: $primary-blue;
        color: $secondary-white;
    }

    &.theme--light {
        background: $primary-light-grey;
        color: $primary-blue;
    }

    &__header > p {
        margin-bottom: 0;
    }

    &__image {
        img {
            object-fit: contain;
            z-index: 0;
            height: 100%;
            width: 50%;
            position: absolute;
            top: 0rem;
            right: 2rem;
        }
    }

    &__body {
        margin-top: 1.25rem;
        @include media-breakpoint-up(lg) {
            padding: 0;
            margin-top: 2rem;
            text-align: left;
            line-height: 1.325;
        }
    }

    .align-left {
        .header-content {
            &__header, &__lead, &__image, &__body {
                text-align: left;
            }
        }
    }
}