div[mx-filter-result] {
    width: fit-content;
}

.product-grid-item {
    margin: 0 auto 5rem;

    &.card-basic {
        @include media-breakpoint-up(md) {
            width: 270px;
        }
        @include media-breakpoint-up(xxl) {
            width: 330px;
        }
    }

    &__content {
        width: 100%;
    }

    &__button-container {
        .btn {
            font-size: toRem(14);
            max-width: 320px;
            padding: 0.6rem 1.2rem 0.5rem;

            &.btn-outline-primary {
                padding: 0.5rem 1rem 0.4rem;
            }
        }
    }
    
    .product-form{
        .price__pricing-group {
            margin-top: 1rem;

            .price-item{
                @extend .h4;
            }
        }
        .swatch-container-label{
            @extend .h5;
            margin-bottom:1rem;
            margin-top:1rem;
            display: inline-block;
        }
        
        .swatch-element-container{
            display: flex;
            flex-wrap: wrap;
            // margin-bottom: 1rem;
            // width: 270px;
            @include media-breakpoint-up(xxl) {
                // width: 330px;
            }
        }
        .single-option-selector{
            width: 50%; 
            margin-top: 0.7rem;
            padding: 0.4rem;
            border-radius: 10px;
            background: transparent;            
        }

        &__item {
            .btn {
                @include media-breakpoint-up(md) {
                    font-size: toRem(14);
                    max-width: 320px;
                    padding: 0.6rem 1.2rem 0.5rem;
                    border-width: 1px;
                }
            }
        }
    }

    .product-qty {
        margin-top:1rem;
        border: 1px solid $primary-blue;
        border-radius: 3rem;

        input {
            height: 2rem;
            padding:0;
            padding-top: 0.325rem;
            margin:auto;
        }
      
        a {
            display: inline-flex;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 2.3rem;
            height: 2.2rem;
            color:$primary-blue;
            border: none;
            text-decoration: none;
            @include body-1;
        }
      }
      .image-wrap{
        background: transparent;
      }

}

