.page-section-testimonials {
    background: $primary-blue;
    padding: 3.5rem 0 3.75rem;
    position: relative;
    text-align: center;
    overflow: hidden;
    z-index: 0;

    &.theme--light {
        background: $primary-light-grey;

        .swiper {
            .swiper-slide {
                .testimonial-body,
                .read-more {
                    color: $primary-blue;
                }

                .read-more {
                    &:hover {
                        color: $secondary-gold;
                    }
                }
            }
        }
    }

    @include media-breakpoint-up(lg) {
        padding: 5rem 0rem;
    }

    @include media-breakpoint-up(xxl) {
        padding: 6rem 0rem;
    }

    .gsap-el {
        opacity: 0;
        transform: translateY(40px);
        transition: all 0.5s;
        
        &.gsap-on {
            opacity: 1;
            transform: translateY(0);
        }
    }

    .swiper {
        padding-right: calc(var(--bs-gutter-x) * .5);
        margin-bottom: 1.5rem;
        @include media-breakpoint-up(md) {
            margin-bottom: 3.125rem;
        }
        @include media-breakpoint-up(lg) {
            margin-bottom: 1.125rem;
        }
    }

    .swiper-testimonials-logo {
        overflow: visible;
    }

    .swiper-wrapper {
        display: flex;
        align-items: center;
        width: 100%; 
        height: 100%;
    }

    .swiper-slide {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        color: $secondary-white; 
        opacity: .25;
        transition: opacity .3s;
        width: fit-content;
        width: 210px;
        @include media-breakpoint-up(lg) {
            width: auto;
        }
        
        &.swiper-slide-active {
            opacity: 1;
        }

        .read-more {
            position: relative;
            color: $secondary-white;
            transition: color 0.3s;
            text-decoration: underline;
            @include media-breakpoint-up(lg) {
                font-size: 1.25rem;

                &:hover {
                    color: $secondary-gold;
                }
            }
        }

        .testimonial-body {
            text-align: center;
            max-width: 45ch;
            font-size: toRem(23);
            letter-spacing: 0.025rem;
            @include media-breakpoint-up(lg) {
                max-width: 40ch;
                line-height: 1.1;
                font-size: toRem(45);
            }
        }

        .logo-holder {
            position: relative;
            cursor: pointer;
            img {
                width: 100%;
                object-fit: contain;
                max-height: 55px;
            }
        }
    }

    .button-container {
        &__inner {
            display: flex;
            align-items: center;
            justify-content: center;

            @include media-breakpoint-up(md) {
                gap: 0.625rem;
            }
        }

        .swiper-button {
            &:hover {
                circle {
                    fill: $secondary-gold;
                    stroke: $secondary-gold;
                }
            }
        }
    }
}
