@import url("https://fast.fonts.net/lt/1.css?apiType=css&c=74869573-60b5-4972-a58a-4e93db656400&fontids=5179382");
@font-face {
    font-family: "SaaSeriesED W05 Regular";
    font-display: swap;
    src: url("./Fonts/5179382/fee4385d-34e1-4ec1-81b4-09f828b9c62b.woff2") format("woff2"), url("./Fonts/5179382/3be6a0ba-0c9d-4c0e-bac1-87c077e4aa12.woff") format("woff");
}
@font-face {
    font-family: "ITCClearfaceRegular";
    font-display: swap;
    src: url('./Fonts/ITCClearfaceRegular/font.woff2') format('woff2'), url('./Fonts/ITCClearfaceRegular/font.woff') format('woff');
}
@font-face {
    font-family: "EphemeraEgyptianCondensedSemibold";
    font-display: swap;
    src: url('./Fonts/EphemeraEgyptianCondensedSemibold/font.woff2') format('woff2'), url('./Fonts/EphemeraEgyptianCondensedSemibold/font.woff') format('woff');
}
@font-face {
    font-family: "EphemeraEgyptianCondensedMedium";
    font-display: swap;
    src: url('./Fonts/EphemeraEgyptianCondensedMedium/font.woff2') format('woff2'), url('./Fonts/EphemeraEgyptianCondensedMedium/font.woff') format('woff');
}
@font-face {
    font-family: "BornReadySlanted";
    font-display: swap;
    src: url('./Fonts/BornReadySlanted/font.woff2') format('woff2'), url('./Fonts/BornReadySlanted/font.woff') format('woff');
}

$SaaSeries : "SaaSeriesED W05 Regular";
$ITCClearface: "ITCClearfaceRegular";
$EphemeraEgyptianSemiBold: "EphemeraEgyptianCondensedSemibold";
$EphemeraEgyptianMedium: "EphemeraEgyptianCondensedMedium";
$BornReady: "BornReadySlanted";

.font-saa { font-family: $SaaSeries; }
.font-itc { font-family: $ITCClearface;}
.font-ee-semibold { font-family: $EphemeraEgyptianSemiBold; }
.font-ee-medium { font-family: $EphemeraEgyptianMedium; }
body{ color:$primary-blue; }

/** Headline Sizes **/ 
$font-size-header-1: toRem(116);
$font-size-header-3: toRem(53);
$font-size-header-4: toRem(28);
$font-size-header-5: toRem(22);

/** Body Type Sizes **/ 
$font-size-body-1: toRem(21);
$font-size-body-2: toRem(16);

@mixin header-1 {
    font-family: $EphemeraEgyptianMedium;
    font-weight: normal;
    font-size: toRem(42);
    line-height: toRem(40);
    letter-spacing: -0.01rem; // Equals -10 in milli ems from XD units
    text-transform: uppercase;
    @include media-breakpoint-up(md) {
        font-size: toRem(80);
        line-height: toRem(80);
    }
}

@mixin header-2 {
    font-family: $EphemeraEgyptianMedium;
    font-weight: normal;
    font-size: toRem(24);
    line-height: toRem(32);
    letter-spacing: -0.01rem;
    text-transform: uppercase;
    @include media-breakpoint-up(md) {
        font-size: toRem(42);
        line-height: toRem(46);
    }
}

@mixin header-3 {
    font-family: $ITCClearface;
    text-transform: none;
    font-size: toRem(28);
    line-height: toRem(36);
    letter-spacing: -0.01rem;

}
@mixin header-4 {
    font-family: $SaaSeries;
    text-transform: none;
    font-size: toRem(22);
    line-height: toRem(24);
    letter-spacing: -0.01rem;
}

@mixin header-5 {
    font-family: $SaaSeries;
    text-transform: none;
    font-size: toRem(14);
    line-height: toRem(16);
    letter-spacing: -0.01rem;
}

@mixin header-6 {
    font-family: $SaaSeries;
    text-transform: none;
    font-size: toRem(12);
    line-height: toRem(14);
    letter-spacing: -0.01rem;
}

@mixin body-1 {
    font-family: $ITCClearface;
    text-transform: none;
    font-size: toRem(21);
    line-height: toRem(28);
}

@mixin body-2 {
    font-family: $ITCClearface;
    text-transform: none;
    font-size: toRem(16);
    line-height: toRem(24);
}


// Updates Overhaul
.header-1, .rte h1, h1{ @include header-1; }
.header-2, .rte h2, h2{ @include header-2; }
.header-3, .rte h3, h3{ @include header-3; }
.header-4, .rte h4, h4{ @include header-4; }
.header-5, .rte h5, h5{ @include header-5; }
.header-6, .rte h6, h6{ @include header-6; }
.body-1 { @include body-1; }
.body-2 { @include body-2; }

* {
    -webkit-font-smoothing: antialiased;
}