.hero-carousel {
  position: relative;
  margin-top: -5rem;
  overflow: hidden;

  @include media-breakpoint-up(md) {
    max-height: 950px;
    margin-top: -7rem;
  }

  @include media-breakpoint-up(lg) {
    max-height: none;
    height: calc(103vh + 1.5rem);
  }

  @media (max-width: 1999px) and (max-height: 727px) {
    min-height: 650px;

    .header-1 {
      font-size: toRem(54);
      line-height: 1;
    }
  }

  .slide-controls {
    display: flex;
    gap: 0.75rem;
    margin-top: 0;
    position: absolute;
    bottom: 3%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;

    button {
      width: 2.5rem;
      height: 2.5rem;
      min-width: 2.5rem;
      min-height: 2.5rem;
    }

    button {
      circle {
        stroke: white;
        fill: white;
      }

      &:hover {
        circle {
          stroke: $secondary-gold;
          fill: $secondary-gold;
        }
      }
    }

    @include media-breakpoint-up(md) {
      position: absolute;
      bottom: 10%;
      // right: 5%;
      justify-content: flex-end;
      margin-top: 0;
      z-index: 1;
    }
  }

  .hero-carousel__slide {
    &--wrapper {
      isolation: isolate;
    }

    &--content {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: start;
      margin-top: 7.75rem;
      margin-bottom: 3.5rem;
      z-index: 1;

      @include media-breakpoint-up(md) {
        position: relative;
        align-items: start;
        margin-top: 15.5rem;
        margin-bottom: 7.75rem;
      }

      @include media-breakpoint-up(xl) {
        margin-top: 0;
        top: calc(50% + 4rem);
        transform: translateY(-60%);

        &.slide-content--single {
          position: absolute;
          left: inherit;
        }
      }
    }

    &--background {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      isolation: isolate;
      z-index: 0;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }

      .filter {
        background: linear-gradient(rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 150%);
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 1;

        @include media-breakpoint-up(md) {
          background: linear-gradient(rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 70%);
        }

        @include media-breakpoint-up(lg) {
          background: linear-gradient(rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 40%);
        }
    
        &.desktop {
          background: linear-gradient(
            90deg,
            rgba(0, 0, 0, 0.38) 0%,
            rgba(0, 0, 0, 0.28) 33%,
            rgba(0, 0, 0, 0) 100%
          );
        }
      }
    }
  }

  .swiper,
  .swiper-slide {
    height: 100%;
  }

  .swiper {
    padding-right: 0;
  }

  .swiper-slide {
    padding-bottom: 2.5rem;
    height: auto;
  }
  
  .hero-section__header {
    position: relative;
    padding: 0 1rem;
    z-index: 1;
    text-shadow: 5px 15px 40px rgba(8, 24, 47, 0.6);
    @include media-breakpoint-up(lg) {
      padding-left: 0;
    }
  }

  .hero-section__subheader {
    padding: 0 1rem;
    text-shadow: 2px 3px 24px rgba(8, 24, 47, 1);
    @include media-breakpoint-up(lg) {
      padding-left: 0;
    }

    p {
      padding-bottom: 0;
      margin-bottom: 0;
    }
  }

  // Theme Colors
  .theme--light {
    .hero-section__subheader,
    .hero-section__header {
      color: $secondary-white;
    }
  }
  
  .theme--dark {
    .hero-section__subheader,
    .hero-section__header {
      color: $primary-blue;
    }
  }

  // Text Alignment
  .alignment--center {
    .hero-section__header {
      text-align: center;
      margin: 0 auto;
      @include media-breakpoint-up(lg) {
        text-align: left;
        margin: 0;
      }
    }
    .hero-section__subheader {
      text-align: center;
      margin: 0 auto;
      @include media-breakpoint-up(lg) {
        text-align: left;
        margin: 0;
      }
    }
    .content-wrap {
      justify-content: center;
      @include media-breakpoint-up(lg) {
        align-items: start;
      }
    }
    .hero-section__btn {
      margin: 0 auto;
      @include media-breakpoint-up(lg) {
        margin: 0;
      }
    }
  }
}
