.fish-calculator-modal {

    &.show {
        background: rgba($secondary-blue, .5);
    }

    .modal-dialog-centered {
        top: 1vh;
        @include media-breakpoint-up(lg) {
            max-width: 98%;
        }
    }

    .modal-content {
        background: $primary-blue;
        color: $secondary-white;
        text-align: center;
        padding: 4rem 0rem;
        border-radius: 1.5rem;
        height: 97vh;
        @include media-breakpoint-up(md) {
            padding: 6rem;
            min-height: 700px;
        }
        @include media-breakpoint-up(lg) {
            height: 98vh;
        }

        .modal-body {
            @include media-breakpoint-up(md) {
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }

        .btn-close {
            right: .5rem;
            top: .5rem;
            left: initial;
            bottom: initial;
            @include media-breakpoint-up(md) {
                right: 1rem;
                top: 1rem;
            }
        }

        .modal-title {
            max-width: 18ch;
            margin: 0 auto 2rem auto;
            @include media-breakpoint-up(md) {
                margin-bottom: 2.5rem;
            }
        }

        .answer {
            display: none;
            &.show {
                display: block;
            }
        }

        .panel-1,
        .panel-2 {
            display: none;
            &.show {
                display: block;
            }
        }
        .panel-2 {
            .modal-title {
                margin-bottom: 1.5rem;
            }
            .footer {
                margin: 0 auto 2rem auto;
                max-width: 42ch;
            }
        }
    }
    
    form {
        display: flex;
        justify-content: center;
        column-gap: .5rem;
        margin-bottom: 2.5rem;
        @include media-breakpoint-up(md) {
            margin-bottom: 3.25rem;
            gap: 1rem;
        }
    }

    .button-wrapper {
        flex-shrink: 0;
    }
      
    .range-button {
        color: $secondary-white;
        cursor: pointer;
        user-select: none;

        .text-container {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            height: 100%;
            border: 2px solid $secondary-white;
            width: toRem(68);
            height: toRem(68);
            transition: .3s;
            margin-bottom: .25rem;
            @include media-breakpoint-up(md) {
                width: toRem(166);
                height: toRem(166);
                border-width: 4px;
                margin-bottom: 0;
            }
        }

        .range-text {
            @include header-2;
            font-size: toRem(24);
            line-height: 1;
            font-weight: 500;
            margin-bottom: 0;
            @include media-breakpoint-up(md) {
                font-size: toRem(62);
                margin-bottom: 1rem;
            }
        }
    }
      
    input[type="radio"] {
        /* Add if not using autoprefixer */
        -webkit-appearance: none;
        /* Remove most all native input styles */
        appearance: none;
        /* For iOS < 15 */
        // background-color: var(--form-background);
        /* Not removed via appearance */
        margin: 0;
        border: 0;
        width: 0;
        height: 0;
    } 
    input[type="radio"]:checked + .range-button,
    .range-button:hover {
        .text-container {
            background: $secondary-white;
            color: $primary-blue;
        }
    }

    input[type="radio"]:focus + .range-button,
    input[type="radio"]:focus-visible + .range-butto {
        .text-container {
            border-color: $secondary-gold;
        }
    }
      
}
