.contact-container {
    padding-top: 3rem;
    padding-bottom: 3rem;
    background: $primary-light-grey;
    @include media-breakpoint-up(md) {
        padding-top: 6rem;
        padding-bottom: 5rem;
    }
}

.contact-form {
    .form-group {
        margin-bottom: 1rem;
        margin-top: 1.875rem;
        @include media-breakpoint-up(lg) {
            margin-bottom: 1rem;
        }
    }

    form {
        label {
            margin-bottom: 1.25rem;
            margin-left: 1.25rem;
            font-size: 1rem;
            background: $primary-light-grey;
            color: $primary-blue;
            font-family: $SaaSeries;
            padding: 0 0.5rem;
        }

        .form-control {
            border-radius: 4px;
            background: none;
            padding: 1.2rem 0px 1rem 1.5rem;
            font-size: 1.25rem;
            border: 1px solid $primary-blue;
            margin-top: -2rem;
            font-family: $ITCClearface;
            color: $primary-blue;

            &::placeholder {
                font-size: 1rem;
            }
        }
    }

    .reason {
        position: relative;
        &::after {
            content: "";
            position: absolute;
            right: 2rem;
            top: 2.6rem;
            width: 0; 
            height: 0; 
            border-top: 10px solid $primary-blue;
            border-left: 7px solid transparent;
            border-right: 7px solid transparent;
        }
    }

    .do-not-have, #emailHelp, .error {
        margin-top: 0.75rem;
        font-size: 1rem;
    }

    .errors ul {
        @include header-5;
        list-style: none;
        padding-left: 0;
        font-size: 0.875rem;
    }

    input:not(.btn), select, textarea {
        // color: $secondary-white;
        background: none;
        // border: 1px solid $secondary-white;
        border-radius: 4px;
        margin-top: -2rem;
        padding: 1.2rem 0 1rem 1.5rem;
        font-family: $ITCClearface;
        font-size: 1.25rem;
        width: 100%;
        z-index: 1;

        &:focus, &:focus-visible {
            background: none;
            // color: $secondary-white;
        }

        &::-webkit-search-cancel-button{
            margin-right: 1rem;
        }
    }

    .form-message {
        &--error {
            h2 {
                font-family: $ITCClearface;
            }
            ul {
                a {
                    font-family: $SaaSeries;
                    text-decoration: none;
                }
            }
        }
    }
}