.section-collection-title {
    padding-top: 3rem;
    position: relative;
    z-index: 0;
    @include media-breakpoint-up(md) {
        padding-top: 7rem;
    }
}

.section-product-grid {
    position: relative;
    z-index: 0;

    .filter-ready {
        &::after {
            content: "";
            display: none;
            @include media-breakpoint-up(lg) {  
                display: block;
                flex-basis: 30.5%;
            }
            @include media-breakpoint-up(xl) {  
                flex-basis: 26%;
            }
            @include media-breakpoint-up(xxl) {  
                flex-basis: 27%;
            }
        }
    }

    .card-basic {
        margin-bottom: 5rem;
        @include media-breakpoint-down(md) {
            padding: 0;
        }
        
        .image-wrap {
            height: 310px;
            @include media-breakpoint-up(xxl) {
                width: 330px;
                height: 360px;
            }
            // @include media-breakpoint-up(md) {
            //     width: 330px;
            //     height: 330px;
            // }
            // @include media-breakpoint-up(xxl) {
            //     width: 330px;
            //     height: 330px;
            // }
        }
        
    }


    &.hidden {
        display: none;
    }
}