// Order History
.my-account-container {
    padding-top: 4rem;
    @include media-breakpoint-up(lg) {
        padding-top: 6rem;
        padding-bottom: 2rem;
    }

    h1, h2, h3, h4 {
        font-family: $EphemeraEgyptianMedium;
        position: relative;
    }

    #customer_orders, #customer_detail {
      @include media-breakpoint-up(lg) {
        h2 {
          font-size: toRem(42);
        }
      } 
    }

    // h1 {
    //     font-size: 3.25rem;
    //     @include media-breakpoint-up(lg) {
    //         font-size: 4.25rem;
    //     }
    // }

    // h2 {
    //     font-size: 3.25rem;
    // }

    h4 {
        position: relative;
        font-size: 1.25rem;
        padding-bottom: 1rem;
        margin-bottom: 1rem;
        margin-top: 2rem;
        @include media-breakpoint-up(lg) {
            font-size: 1.5rem;
        }
    }
    
    p {
        font-size: 1rem;
        line-height: 0.75;
    }

    .logout-btn {
        margin-top: 1rem;
        margin-bottom: 3.25rem;
        @include media-breakpoint-up(lg) {
            margin-bottom: 6.5rem;
            margin-left: 9.25%;
        }
    }

    .order-history {
        .order-details {
            display: flex;
            flex-wrap: wrap;
            padding-bottom: 0.5rem;
            margin-top: 2.5rem;

            dl {
                padding-left: 0;
                list-style: none;
                width: 33%;
    
                dt {
                    font-family: $EphemeraEgyptianMedium;
                    font-size: 1.25rem;
                    font-weight: normal;
                    margin-bottom: 1rem;
                    padding-bottom: 0.6rem;
                    border-bottom: 1px solid $primary-blue;
                }

                dd {
                    font-family: $ITCClearface;
                    font-size: 1rem;
                }
                
                a { 
                    color: $primary-blue;
                }

            }
        }
        
        &__desktop {
            thead {
                font-size: 1.25rem;
                line-height: 1.1;
                vertical-align: top;
                color: $primary-blue;
                position: relative;
                height: 2.5rem;
                &::after {
                    content: "";
                    position: absolute;
                    width: 100%;
                    height: 1px;
                    left: 0;
                    bottom: 0;
                    background: $primary-blue;
                }

                th {
                    padding-right: 2rem;
                    font-family: $EphemeraEgyptianMedium;   
                    font-weight: normal;
                    @include media-breakpoint-up(xl) {
                        padding-right: 4rem;
                    }
                    @include media-breakpoint-up(xxl) {
                        padding-right: 6rem;
                    }

                    &:nth-child(2) {
                        padding-right: 6rem;
                    }
                }
            }

            tbody {
                font-family: $ITCClearface;
                font-weight: normal;
                font-size: 1rem;
                line-height: 1.1;
                color: $primary-blue;

                a {
                    color: $primary-blue;
                    transition-duration: 0.4s;
                    &:hover {
                        color: $secondary-gold;
                    }
                }

                td {
                    padding-top: 0.35rem;
                }

                tr {
                    border-bottom: 1px solid $primary-blue;
                    height: 42px;
                    vertical-align: middle;
                }
            }
        }
    }
    
    #customer_detail {
        margin-top: 3.5rem;
        @include media-breakpoint-up(lg) {
            margin-top: 0;
        }

        .view-addresses {
            margin-top: 2rem;
            margin-bottom: 5rem;

            a {
                font-family: $SaaSeries;
                font-size: 0.875rem;
                color: $primary-blue;
                transition-duration: 0.4s;
                &:hover {
                    color: $secondary-gold;
                }
            }
        }
    }
}


// Single Order Details
.order-details-intro {
    padding-top: 4rem;
    color: $primary-blue;
    @include media-breakpoint-up(lg) {
        padding-top: 6rem;
    }

    h1 {
        font-family: $EphemeraEgyptianMedium;
        font-size: 3.25rem;
        margin-bottom: 1.5rem;
        @include media-breakpoint-up(lg) {
            font-size: 4.25rem;
        }
    }
    
    .title {
        margin-bottom: 2rem;
        @include media-breakpoint-up(lg) {
            margin-bottom: 4.5rem;
            margin-left: 4px;
        }
    }
}

.order-details-section {
    margin-bottom: 2rem;
    color: $primary-blue;
    @include media-breakpoint-up(lg) {
        margin-top: 2rem;
        margin-bottom: 4rem;
    }

    .order-title {
        position: relative;
        margin-top: 3rem;
        margin-bottom: 0;
        padding-bottom: 1rem;
        @include media-breakpoint-up(lg) {
            font-size: toRem(42)
        }
    }
    .order-date {
        display: none;
        font-size: 1rem;
        margin-top: 0.5rem;
        @include media-breakpoint-up(lg) {
            display: block;
        }
    }

    dt {
        font-family: $EphemeraEgyptianMedium;
        font-weight: normal;
        font-size: toRem(21);
    }

    dl {
        @include body-1;
        font-size: 1rem;
    }
    
    .order-details {
        dl {
            width: 50%;
        }

        &__mobile {
            @include media-breakpoint-up(lg) {
                display: none;
            }
        }

        &__desktop {
            display: none;
            @include media-breakpoint-up(lg) {
                display: block;
            }
        }
        .detail-block {
            display: flex;
            flex-wrap: wrap;
            margin: 2rem auto 0;
            padding-bottom: 1rem;
            
            dt {
                border-bottom: 1px solid $primary-blue;
                padding-bottom: 0.75rem;
                margin-bottom: 0.75rem;
            }

            a {
                color: $primary-blue;
            }
        }

        .subtotal, .shipping {
            text-align: right;
            margin-bottom: 0.25rem;
        }

        .total {
            text-align: right;
            font-size: 1.5rem;
            margin-top: 1rem;
        }
    }

    #order_details {
        @include media-breakpoint-up(lg) {
            float: left;
        }

        table {
            width: 100%;
        }

        thead {
            font-family: $EphemeraEgyptianMedium;
            font-weight: normal;
            height: 2.5rem;
            position: relative;
            &::after {
                content: "";
                position: absolute;
                width: 100%;
                height: 1px;
                left: 0;
                bottom: 0;
                background: $primary-blue;
            }
            @include media-breakpoint-up(lg) {
                font-size: 1.25rem;
            }

            tr:first-child {
                border-bottom: none;
            }

            td {
                color: $primary-blue;
            }

            th {
                padding-right: 1.75rem;
                font-weight: normal;
            }
        }

        tr {
            vertical-align: middle;
            line-height: 1.25;
            height: 2.25rem;
            border-bottom: 1px solid $primary-blue;
        }

        tbody {            
            .text-center {
                text-align: center;   
                @include media-breakpoint-up(lg) {
                    text-align: left;
                }
            }

            td {
                font-family: $ITCClearface;
                font-size: 1rem;
                padding: 0.875rem 2rem 0.75rem 0;
                @include media-breakpoint-up(xl) {
                    padding-right: 5rem;
                }
                @include media-breakpoint-up(xxl) {
                    padding-right: 6rem;
                }
            }
            
            tr > td > a {
                color: $primary-blue;
            }

            .tr-total {
                border-bottom: none;
                text-align: right;
            }
        }

        .total {
            font-size: 1.5rem;
        }
    }

    .fulfillment-details {
        display: flex;
        flex-direction: column;
        @include media-breakpoint-up(lg) {
            float: right;
            margin-right: 3.3333333%;
        }
        @include media-breakpoint-up(xxl) {
            margin-right: 8.3333333%;
        }

        .alpha, .omega {
            margin-top: 3rem;
            @include media-breakpoint-up(lg) {
                margin-top: 0;
                margin-bottom: 3rem;
            }
    
            h4 {
                font-family: $EphemeraEgyptianMedium;
                font-size: 1.5rem;
                border-bottom: 1px solid $primary-blue;
                padding-bottom: 1rem;
                margin-bottom: 1rem;
                @include media-breakpoint-up(lg) {
                    font-size: 2.5rem;
                }
            }

            .deets-wrap {
                font-family: $ITCClearface;
                font-size: 1rem;
            }
        }
    }
}


// Addresses
#customer-addresses {
  padding-top: 4rem;
  padding-bottom: 4rem;
  @include media-breakpoint-up(lg) {
      padding-top: 6rem;
      padding-bottom: 6rem;
  }

  h1 {
      position: relative;
      margin-bottom: 1.25rem;
  }

  .btn-primary {
      margin-right: calc(var(--bs-gutter-x) * .5);
      margin-left: calc(var(--bs-gutter-x) * .5);
  }

  div[data-address] {
    button {
        &:not(.btn-primary) {
          margin-top: 0;
          font-family: $SaaSeries;
          font-size: 1rem;
          text-transform: uppercase;
          text-decoration: underline;
          border: none;
          background: none;
        }
    }

    .btn-primary {
      text-decoration: none;
    }
  }

  .btn-close {
    width: 3rem;
    height: 3rem;
    left: inherit;
    top: 0.6rem;
    right: 0.6rem;
    background: $primary-blue;
    @include media-breakpoint-up(lg) {
      width: 4rem;
      height: 4rem;
      left: inherit;
      right: 1.5rem;
      top: 1rem;
    } 
    
    svg {
      width: 100%;
      fill: inherit;
      stroke: inherit;
      padding: 0;
      margin-top: 3px;
    }
  }

  ul {
      text-align: left;
  }

  .curent-addresses-col {
    margin-top: 2.5rem;
    color: $primary-blue;
    background-color: $primary-light-grey;

    h2 {
      text-align: left;
      margin-bottom: 1rem;
      @include media-breakpoint-up(lg) {
        // font-size: 1.5rem;
      }
    }

    p {
        font-family: $ITCClearface;
        font-size: 1rem;
        line-height: 1.6;
    }

    .action-button {
        margin: 0 2.5rem 0 0;
        padding-left: 0;
        font-family: $SaaSeries;
        font-size: 1rem;
        text-transform: uppercase;
        text-decoration: underline;
        color: $primary-blue;
        border: none;
    }
  }

  .edit-address, #AddAddress {
      color: $primary-blue;

      h2 {
          @include media-breakpoint-up(lg) {
              margin-top: 3rem;
          }
      }

      form {
          margin-top: 1rem;
      }

      .field, .field-dropdown {
          display: flex;
          flex-direction: column-reverse;

          label {
              color: $primary-blue;
              background: $primary-light-grey;
              margin-bottom: 1.35rem;
              margin-left: 1.25rem;
              padding: 0 0.5rem;
              font-family: $SaaSeries;
              font-size: 1rem;
              z-index: 2;
              width: fit-content;
          }

          input, select {
              color: $primary-blue;
              background: none;
              border: 1px solid $primary-blue;
              border-radius: 4px;
              margin-top: -2rem;
              padding: 1.2rem 0 1rem 1.5rem;
              font-family: $ITCClearface;
              font-size: 1.25rem;
              width: 100%;
              z-index: 1;
          }
      }

      .field-dropdown {
          flex-direction: column;
          margin-top: 2rem;
          
          label {
              margin-bottom: 0.875rem;
          }
      }

      input[name="address[default]"] + label {
          @include header-5;
          font-size: 1rem;
      }
  }

  .account-details-btn {
      margin-bottom: 1.25rem;
      width: 200px;
  }

  .btn-reverse {
      width: 200px;
      a {
          text-decoration: none;
          color: $primary-blue;
      }
      &:hover {
          color: $secondary-white;
          a {
              color: $secondary-white;
          }
      }
  }

  #address_tables {
      margin-top: 5rem;
      width: 100%;

      .address_table {
          width: 100%;
      }

      .address_title {
          position: relative;
          font-size: 1.25rem;
          padding-bottom: 0.75rem;
          &::after {
              content: "";
              position: absolute;
              width: 100%;
              height: 2px;
              left: 0;
              bottom: 0;
              background: $primary-blue;
          }
      }

      .view_address {
          font-size: 1rem;
          line-height: 1.5;
          margin-top: 0.75rem;
          text-transform: capitalize;

          p {
              margin-bottom: 0;
          }

          .address_actions {
              font-size: 1rem;
              margin-top: 1.25rem;
              margin-bottom: 3rem;
  
              span a {
                  color: $black;
                  margin-right: 2rem;
              }
          }
      }
  }

  // Forms
  #add_address {
      display: none;
      &.showForm {
          display: block;
      }
  }
  .edit_address {
      display: none;
      &.showForm {
          display: block;
      }
  }

  #add_address, .edit_address {
      h4 {
          font-family: $EphemeraEgyptianMedium;
          font-size: 1.5rem;
          text-transform: uppercase;
          margin-top: 3rem;
          margin-bottom: 1.5rem;
          @include media-breakpoint-up(lg) {
              font-size: 2.5rem;
              margin-top: 4rem;
          }
      }
      
      form {
          label {
              margin-bottom: 1.25rem;
              margin-left: 1.25rem;
              font-size: 1rem;
              background: $primary-light-grey;
              font-family: $SaaSeries;
              padding: 0 0.5rem;
              
          }

          input, select  {
              border-radius: 100px;
              background: none;
              padding: 1.2rem 0px 1rem 1.5rem;
              font-size: 1.25rem;
              border: 1px solid $primary-blue;
              width: 100%;
              margin-bottom: 1.5rem;

              &::placeholder {
                  font-size: 1rem;
              }
          }
          select::after {
              margin-right: 1rem;
          }

          .customer_address_table {
              width: 100%;
              max-width: 700px;
          }

          tr {
              width: 100%;
              display: flex;
              flex-direction: column;
              
              .value {
                  width: 100%;
                  font-size: 1rem;

                  input {
                      width: 100%;
                      margin-top: 0.4rem;
                      margin-right: 0.75rem;
                  }
                  input[type=checkbox] {
                      width: auto;
                  }
              }
              a {
                  margin-bottom: 2rem;
                  &:visited {
                      color: $black;
                  }
              }
          }
          
          .action_bottom  {
              margin-bottom: 5rem;
              display: flex;
              align-items: center;
              flex-wrap: wrap;

              input {
                  width: 220px;
                  margin-bottom: 0;
                  margin-right: 1.5rem;
                  padding-left: 0;
                  padding-top: 0.7rem;
                  background: $secondary-blue;
                  color: $secondary-white;
                  border: none;
              }
              .note a, p {
                  font-size: 1rem;
                  color: $black;
              }
              p {
                  width: 100%;
                  margin-top: 1rem;
              }
          }
      }
  }
  
  .modal {
    &-dialog {
      position: absolute;
      top: 2rem;
      left: 50%;
      transform: translateX(-50%);
      max-width: 100%;
      width: 90%;
      @include media-breakpoint-up(md) {
        top: 4rem;
        left: 50%;
        transform: translateX(-50%);
        width: 80%;
      }
      @include media-breakpoint-up(lg) {
        top: 6rem;
        width: 70%;
        max-width: 950px;
      }
    }
    
    &-content {
      padding: 1rem;
      margin-bottom: 10rem;
      background: $primary-light-grey;
    }
  }
}





