.blog-template {
    padding-top: 3rem;
    padding-bottom: 3rem;
    @include media-breakpoint-up(lg) {
        padding-bottom: 5rem;
        padding-top: 5rem;
    }

    .back-button {
        font-size: 1rem;
    }

    &__featured {
        position: relative;
        margin-bottom: 3rem;
        @include media-breakpoint-up(lg) {
            margin-bottom: 7rem;
        }

        &--title {
            font-size: 2.875rem;
            line-height: 1;
            @include media-breakpoint-up(lg) {
                font-size: 5rem;
            }
        }

        &--content {
            max-width: 30ch;
            @include media-breakpoint-up(md) {
                max-width: 50ch;
            }
        }

        &--image {
            overflow: hidden;
            @include media-breakpoint-up(md) {
                order: 1;
            }
            @include media-breakpoint-up(lg) {
                max-height: 450px;
            }
            
            img {
                width: 100%;
                height: auto;
                object-fit: cover;
            }
        }

        &--url {
            width: fit-content;
        }
    }
    
    .article-grid {
        @include media-breakpoint-up(lg) {
            margin-top: 4rem;
        }

        .card-basic {
            @include media-breakpoint-down(sm) {
                width: 100%;
            }

            h2 {
                max-width: 100%;
            }
        }

        .row {
            @include media-breakpoint-up(lg) {
                &::after {
                    content: "";
                    width: 30%;
                }
            }

            &.grid {
                @include media-breakpoint-up(lg) {
                    display: grid;
                    grid-auto-rows: minmax(0,auto);
                    grid-template-columns: repeat(4,1fr);
                    grid-gap: 0rem 1rem;
                }

                .article-grid__block {
                    width: 100%;

                    .article-grid__image {
                        @include media-breakpoint-up(lg) {
                            height: 175px;
                        }
                    }
                    
                    &:nth-child(1), &:nth-child(10) {
                        @include media-breakpoint-up(lg) {
                            grid-area: 1 / 1 / 3 / 3;
                            
                            .article-grid__content {
                                display: block;
                                max-width: 90%;
                            }
        
                            .article-grid__image {
                                height: 350px;
                            }
                        }
                    }
        
                    &:nth-child(10) {
                        @include media-breakpoint-up(lg) {
                            grid-area: 3 / 3 / 5 / 5;
                            margin-top: 2rem;
                        }
                    }
        
                    &:nth-child(6), &:nth-child(7) {
                        @include media-breakpoint-up(lg) {
                            margin-top: 2rem;
                        }
                    }
                }
            }
        }

        &__block, .ghost-grid-item {
            margin-top: 3rem;
            @include media-breakpoint-up(lg) {
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                width: 30%;
                margin-right: 5%;
                margin-top: 0;
                &:nth-of-type(3n) {
                    margin-right: 0;
                }
            }
            @include media-breakpoint-up(xxl) {
                width: 28%;
                margin-right: 7%;
            }
        }
        .ghost-grid-item{
            display: none;
            @include media-breakpoint-up(lg) {
                display: block;
            }
            // background-color: $secondary-white;
            height: 330px;
            cursor: pointer;
            a{
                height: 100%;
            }
        }
        
        &__image {
            height: 250px;
            width: 100%;
            position: relative;
            @include media-breakpoint-up(lg) {
                height: 270px;
            }
            @include media-breakpoint-up(xxl) {
                height: 330px;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            .filter {
                opacity: 0;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: $primary-blue;
                z-index: 2;
                pointer-events: none;
                transition: opacity 0.3s;
            }
        }

        &__url {
            text-decoration: none;
            color: $primary-blue;
            &:hover, &:focus, &:focus-visible {
                .article-grid__image > .filter {
                    opacity: 0.5;
                }
            }
        }

        &__content {
            display: block;
            @include media-breakpoint-up(lg) {
                display: none;
            }
        }

        &__btn {
            width: fit-content;
        }        
    }

    #blogFilter {
        border-bottom: 1px solid $primary-blue;
        padding-bottom: 2rem;

        .blog-select {
            position: relative;
            margin-right: 4rem;
            color: $primary-blue;
            cursor: pointer;

            &::after {
                content: "";
                position: absolute;
                top: 0.55rem;
                right: 0;
                width: 0; 
                height: 0; 
                border-left: 0.375rem solid transparent;
                border-right: 0.375rem solid transparent;
                border-top: 0.5rem solid $primary-blue;
            }

            select {
                font-family: $SaaSeries;
                font-size: 0.875rem;
                min-width: 8rem;
                background: none;
                border: none;
                color: $primary-blue;
                -webkit-appearance: none;
                -moz-appearance: none;
                cursor: pointer;
            }
        }
        a{
            color:$primary-blue;
            text-decoration: none;
            margin-top:.4rem;
        }
    }

    .blog-filter {
        border-top: 2px solid $primary-blue;

        .btn-primary {
            width: fit-content;
        }

        .tag-count {
            margin-top: 1.5rem;
            margin-bottom: -2rem;
        }
    }

    .pagination {
        margin-bottom: 7rem;
    }
}

.share-container {
    display: flex;
    align-items: center;
    list-style: none;
    padding-left: 0;

    li {
        .st-btn {
            border: none;
            background: none;
            transform: scale(1);
            transition-duration: 0.3s;
            
            svg {
                width: 1.25rem;
                margin-right: 0.5rem;
            }
    
            &:hover {
                transform: scale(1.2);
            }
        }
    }
}

// Glossary
#glossary-items {
    h2 {
        margin-top: 4rem;
    }

    img {
        max-width: 100%;
        height: 185px;
        object-fit: cover;
        @include media-breakpoint-up(lg) {
            height: auto;
        }
    }
}