.product-callout-slider {
    padding-top: 2.5rem;
    padding-bottom: 3rem;
    
    .swiper-slide {
        .card-basic .image-wrap {
            background: transparent;
        }
        @include media-breakpoint-up(lg) {
            opacity: 0;
            transform: translateY(40px);
            transition: all 0.5s;

            .card-basic {
                .image-wrap {
                    img {
                        object-fit: contain;
                    }

                    .image-hover {
                        opacity: 0;
                        position: absolute;
                        left: 0;
                        top: 0;
                        object-fit: cover;
                        transition: opacity 0.3s;
                    }
                }

                &:hover {
                    .image-hover {
                        opacity: 1;
                    }
                } 
            }
            
            &.gsap-on {
                opacity: 1;
                transform: translateY(0);
            }
            
        }
    }
}
