.testimonial-image {
    padding-bottom: 3rem;
    @include media-breakpoint-up(lg) {
        padding-bottom: 1.5rem;
    }

    &__content-wrap {
        position: relative;
        margin-top: 1.25rem;

        .image_full {
            width: 100%;
            height: 225px;
            object-fit: cover;
            @include media-breakpoint-up(md) {
                height: 550px;      
            }
            @include media-breakpoint-up(lg) {
                height: 800px;      
            }
        }

        .testimonial-block {
            position: relative;
            padding: 1.5rem 2rem 0rem 2rem;
            color: $primary-blue;
            z-index: 0;
            @include media-breakpoint-up(md) {
                position: absolute;
                bottom: 0;  
                right: 0;
                width: 55%;
                padding: 2rem 2rem 1rem 2rem;
                text-align: right;
                background: $primary-light-grey;
            }
            @include media-breakpoint-up(lg) {
                padding: 4rem 2rem 4rem 8rem;
                width: 45%;
                max-width: 630px;
            }

            &__author {
                margin-top: 2.25rem;
            }
        }
    }
}