.faqs-section {
    background: $primary-light-grey;
    padding-bottom: 4rem;
    padding-top: 3.5rem;
    @include media-breakpoint-up(lg) {
        padding-top: 3rem;
        padding-bottom: 6rem;
    }

    &__header {
        position: relative;

        &--image {
            position: absolute;
            left: 0;
            top: 5rem;
            max-height: 450px;
            z-index: 0;
            @include media-breakpoint-up(xl) {
                left: 2rem;
            }
            @include media-breakpoint-up(xxl) {
                left: 6rem;
            }
        }
    }

    .accordion .accordion-item .accordion-header .accordion-button {
        align-items: flex-start;

        &__header {
            text-transform: none;
        }
    }
}
