// Container / Grid
$grid-columns: 12;
$grid-gutter-width: 10px;


// Bootstrap color overrides
:root  {
    --bs-body-bg: #F2F2F2;
}

body {
    overflow-x: hidden;
    overflow-y: scroll;
    padding-top: 4rem;
    @include media-breakpoint-up(md){
        padding-top: 5rem;
    }
    @include media-breakpoint-up(lg){
        padding-top: 6rem;
    }
}

a {
    color: $secondary-gold;
    transition: 0.3s color;
    @include media-breakpoint-up(lg) {
        &:hover  {
            color: $tertiary-gold;
        }
    }
}

.offset-laptop {
    @media (min-width: 1600px) {
        margin-left: 0;
    }
}

html {
     scroll-behavior: smooth; 
}

.shopify-section {
    position: relative;
}

.anchor-fix {
    position: absolute;
    top: -5rem;
    left: 0;
    height: 0;
}

.two-column-swiper {
    .container {
        .anchor-fix {
            top: -9rem;
        }
    }
}

.template-404 {
    .page-width {
        padding-top: 7rem;
        padding-bottom: 7rem;
        @include media-breakpoint-up(lg) {
            padding-top: 11rem;
            padding-bottom: 11rem;
        }
    }
}