.cart-drawer {

	&__line_items{
		max-height: calc(80vh - 150px);
		overflow: auto;
	}
	.line-item {
		margin: 0;
		padding: 2em 1em;
		button{
			color: white;
			background: transparent;
			width: 1.25em;
			padding: 0;
			height: 1.75em;
			border: none;
			margin-right: 0;
			&.remove{
				margin-left: 1em;
				fill: $white;
				&:hover {
					color: $red;
				}
			}
		}
		&__quantity {
			float:left;
			button{
			}
			input[type='text']{
				background-color: transparent;
				color: white;
				border-left: 0px;
				border-right: 0px;
				width: 2em;
				height: 1.75em;
				padding-left: .25em;
				margin-right: 0;
			}
		}
		&__variant {
			font-size: $font-size-sm;
			display:inline-block;
			width: 100%;
			color: $gray-200;
		}
		&__price {
			float: right;
		}
		&__image{
			background:white;
			display: flex;
			justify-content: center;
			align-items: center;
			padding:5px;
			img{
				max-height:70px;
				max-width: 100%;
			}
		}
	}
	&__footer{
		background: $primary;
		position: absolute;
		bottom: 0;
		left: 15px;
		right: 15px;
		min-height: 130px;
		padding-bottom: 30px;
		.subtotal{
			display: flex;
			justify-content: space-between;
			padding-top: 1em;
		}
		p, label{
			margin-bottom:0px;
			font-size:.8rem;
		}
	}

}


body {

    &.cart-drawer-open {
        .cart-drawer-overlay {
            display: block !important;
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            width: 100vw;
            height: 100vh;
            background: $primary-blue;
            opacity: 0.85;
            z-index: 11;
            cursor: pointer;
        }
    }
}
