[sh-cart-alert]{
    position:fixed;
    bottom:0;
    width:100%;
    padding:1rem 1rem;
    display:flex;
    justify-content:space-around;
    align-items:center;
    z-index: 999999999999;
    background:white;
    transition:all .2s cubic-bezier(.17,.67,.37,.87);
    box-shadow: 0 0 0 0;
    transform: translateY(100%); 

    &.active{
        box-shadow: -10px 0px 46px -11px;
        transition:all .2s cubic-bezier(.17,.67,.37,.87);
        transform: translateY(0);
    }

    p{
        margin-bottom:0;
    }
    .sh-cart-alert-text{
        width:60%;
    }
    .sh-cart-alert-actions{
        width:40%;
        text-align:right;
    }
    .btn-close{
        width: 32px;
        height: 32px;
        border-radius: 50%;
        border: 1px solid $primary-blue;
        opacity: 1;
        color: $primary-blue;
        font-family: sans-serif;
        display: inline-flex;
        align-items: center;
        justify-content: center;			
    }
}
