.media-gallery {
    position: relative;
    background: $primary-light-grey;
    padding-top: 3rem;
    padding-bottom: 5rem;
    overflow: hidden;
    @include media-breakpoint-up(lg) {
        padding-bottom: 10rem;
        padding-top: 7rem;
    }



    &__image {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 400px;
        object-fit: contain;
    }

    &__title {
        color: $primary-blue;
        margin-bottom: 1.5rem;
        @include media-breakpoint-up(lg) {
            margin-bottom: 3rem;
        }
    }

    .mosaic {
        position: relative;
        display: grid;
        grid-template-columns: repeat(2,1fr);
        grid-auto-rows: minmax(0px, auto);
        grid-gap: 0.5rem;
        height: auto;
        @include media-breakpoint-up(lg) {
            grid-template-columns: repeat(5,1fr);
            grid-auto-rows: minmax(0px, auto);
            display: grid;
            grid-gap: 1rem;
        }

        &__block {
            position: relative;
            height: fit-content;
            width: 100%;
            height: 145px;
            transition-duration: 0.3s;
            @include media-breakpoint-up(md) {
                height: 290px;
            }
            @include media-breakpoint-up(lg) {
                height: 200px;
            }

            &:hover {
                filter: none !important;
            }

            &.darken {
                filter: brightness(0.7);
                transition-duration: 0.3s;
            }

            &--image {
                width: 100%;
                height: 100%;
                object-fit: cover;
                transition: opacity 0.3s;
            }

            &:nth-child(5) {
                height: 205px;
                grid-column: 1 / 3;
                @include media-breakpoint-up(md) {
                    height: 325px;
                }
                @include media-breakpoint-up(lg) {
                    grid-row: 1 / 3;
                    grid-column: 3 / 6;
                    height: 100%;
                }
            }

            &:nth-child(6) {
                height: 205px;
                grid-column: 1 / 3;
                @include media-breakpoint-up(md) {
                    height: 325px;
                }
                @include media-breakpoint-up(lg) {
                    grid-row: 3 / 6;
                    grid-column: 1 / 4;
                    height: 96.2%;
                }
            }

            &--button {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                border: none;
            }

            svg {
                position: absolute;
                bottom: 0.5rem;
                right: 0.6rem;
                width: toRem(36);
                height: toRem(36);
                padding: 0.25rem;
                background: $primary-blue;
                border-radius: 50%;
                pointer-events: none;

                &.icon-video {
                    padding: 0.6rem;
                }
            }
        }
    }
}
