.swiper, .swiper-offset {
    overflow-x: clip;
    overflow-y: hidden;
    @include media-breakpoint-down(md) {
        padding-right: 5rem;
        padding-left: calc(var(--bs-gutter-x) * 1);
        margin-left: 0;
        margin-right: 0;
        max-width: 100%;
    }
    @include media-breakpoint-up(md) {
        overflow: hidden;
        margin-left: auto;
        margin-right: auto;
        padding-right: 3rem;
    }
    @include media-breakpoint-up(lg) {
        padding-right: 0;
    }
    
    &-wrapper {
        display: flex;
        width: fit-content;
        flex-wrap: nowrap;
        margin: 0 auto;
        @include media-breakpoint-up(md) {
            // justify-content: space-between;
            width: 100%;
        }
    }
    
    &-slide {
        position: relative;
        height: fit-content;
        width: 270px;
        @include media-breakpoint-up(lg) {
            &:hover {
                .image-wrap > .filter {
                    opacity: 0.5;
                }
            }
        }
    }

    &-pagination {
        display: flex;
        justify-content: start;

        &-bullet {
            width: 0.625rem;
            height: 0.625rem;
            margin-right: 0.5rem;
            border: 1px solid $primary-blue;
            border-radius: 50%;
            transition-duration: 0.3s;

            &-active {
                background: $primary-blue;
            }
        }

        &.theme--dark {
            .swiper-pagination-bullet {
                width: 0.625rem;
                height: 0.625rem;
                margin-right: 0.5rem;
                border: 1px solid $secondary-white;
                border-radius: 50%;
                transition-duration: 0.3s;
    
                &-active {
                    background: $secondary-white;
                }
            }   
        }
    }

    .swiper-button-prev, .swiper-button-next {
        &.disabled{
            pointer-events: none;
            opacity: .2;
        }
    }
}