.store-locator {
    padding-top: 3rem;
    padding-bottom: 3rem;
    @include media-breakpoint-up(lg) {
        padding-top: 8rem;
        padding-bottom: 6rem;
    }
}

.modal-destini {
    .modal-dialog {
        margin-top: 0;
        padding-bottom: 4rem;
        top: 5vh;
        @include media-breakpoint-up(md) {
            max-width: 70%;
        }
        @include media-breakpoint-up(lg) {
            top: 5vh;
        }
        @include media-breakpoint-up(xl) {
            max-width: 80%;
        }
    }
}