// Global Modal Styles for Video Playback
.modal {
    &.show {
        background: #062038cc;
    }

    &-body {
        iframe, video {
            width: 100%;
            height: 240px;
            @include media-breakpoint-up(md) {
                height: 450px;
            }
            @include media-breakpoint-up(lg) {
                height: 70vh;
            }
        }

        .swiper {
            overflow: hidden;
            padding-right: 0;
            padding-left: 0;

            .swiper-wrapper {
                .swiper-slide {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    background: $primary-light-grey;
                    @include media-breakpoint-up(md) {
                        align-items: initial;
                    }
                    
                    &__image, &__video, iframe {
                        width: 100%;
                        height: auto;
                        min-height: 220px;
                        @include media-breakpoint-up(md) {
                            height: 450px;
                        }
                        @include media-breakpoint-up(lg) {
                            height: 65vh;
                        }
                    }
    
                    &__image {
                        object-fit: contain;
                        min-height: 0;
                    }
    
                    &__caption {
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;
                        font-family: $ITCClearface;
                        font-size: toRem(14);
                        line-height: 1.4;
                        margin: 0;
                        color: $black;
                        @include media-breakpoint-up(md) {                
                            font-size: 1rem;
                        }
                        @include media-breakpoint-up(lg) {                
                            flex-direction: row;
                            justify-content: space-between;
                            align-items: center;
                            font-size: toRem(19);
                            text-align: left;
                        }
                    }
    
                    &__link {
                        position: relative;
                        max-width: 290px;
                        @include media-breakpoint-up(md) {       
                            max-width: 320px;
                        }
                    }
                }
            }
        }

        .swiper-button {
            position: absolute;
            top: 40%;
            width: 2.25rem;
            height: 2.25rem;
            border-radius: 50%;
            z-index: 1;
            @include media-breakpoint-up(md) {                
                width: 3.5rem;
                height: 3.5rem;
                top: 230px;
            }
            @include media-breakpoint-up(lg) {
                top: 50%;
                transform: translateY(-50%);
                width: 4.25rem;
                height: 4.25rem;
            }

            &-prev {
                left: -1rem;
                @include media-breakpoint-up(md) {
                    left: -1.8rem;
                }
            }

            &-next {
                right: -1rem;
                @include media-breakpoint-up(md) {
                    right: -1.8rem;
                }
            }
        }
    }
    
    .btn-close {
        position: absolute;
        top: -1.9rem;
        right: -0.8rem;
        opacity: 1;
        cursor: pointer;
        @include media-breakpoint-up(md) {
            top: -2rem;
            right: -1.3rem;
        }
        @include media-breakpoint-up(lg) {
            position: absolute;
            right: 1.625rem;
            top: 1.625rem;
        }
    }
    
    &-dialog {
        top: 30%;
        max-width: 85%;
        margin: 0 auto;
        @include media-breakpoint-up(md) {
            top: 25%;
        }
        @include media-breakpoint-up(lg) {
            max-width: 75%;
            top: 10vh;
        }
        @media (min-width: 2000px) {
            top: 12.5vh;
            max-width: 1450px;
        }
    }

    &-footer {
        border: none;
    }


    // Overrides for Media Gallery Section
    &.media-gallery-modal {
        .modal-dialog {
            top: 20vh;
            @include media-breakpoint-up(md) {
                top: 17vh;
            }
            @include media-breakpoint-up(lg) {
                top: 7vh;
            }
            @include media-breakpoint-up(xxl) {
                top: 10vh;
            }
        }
        .modal-body {
            @include media-breakpoint-down(md) {
                max-height: 600px;
            }
        }
    }
    

    // Used with Modals for Filtering
    &-filters {
        .modal-dialog {
            top: 0;
            max-width: 100%;
            height: 100%;
        }

        .modal-content {
            background: $primary-blue;
            color: $secondary-white;
            padding: 5.5rem 0 5.5rem 1rem;
            height: 100%;
    
            .filters {
                &-cat {
                    font-size: 1rem;
                    margin-bottom: 1rem;
                    margin-top: 2.25rem;
                }

                &-header {
                    font-size: 3.3125rem;
                }

                &-filter {
                    font-size: 0.875rem;
                    
                    a {
                        color: $white;
                        text-decoration: none;
    
                        svg {
                            transform-origin: center;
                            width: 8px;
                            height: 7px;
                            margin-left: 0.375rem;
                            transform: rotate(-90deg);
                        }
                    }
                }
            }
    
            .btn-close {
                top: 1rem;
                left: inherit;
                right: 1rem;
            }
        }

        .modal-body {
            ul {
                padding-left: 0;

                li {
                    list-style: none;
                    margin-bottom: 2rem;

                    &.list-child {
                        margin-bottom: 0;
                        margin-top: 1rem;
                        padding-left: 1rem;
                    }

                    a {
                        text-decoration: none;
                    }
                }
            }

            label {
                @include header-5;
            }

            input {
                border: 1px solid $secondary-white;
            }

            input[type=checkbox]:checked {
                border-color: $secondary-white;
            }

            input[type=checkbox]:after {
                border-bottom: 1px solid $secondary-white;
                border-left: 1px solid $secondary-white;
            }
        }
    }
}