.search-results, .search-bar {
    padding-top: 3.5rem;
    padding-bottom: 4rem;
    
    &__header {
        font-size: 3rem;
        @include media-breakpoint-up(md) {
            font-size: toRem(96);
        }
    }

    .form-group {
        @include media-breakpoint-up(md) {
            display: flex;
        }
    }

    label {
        color: $primary-blue;
        background: $primary-light-grey;
        margin-bottom: 1.35rem;
        margin-left: 1.25rem;
        padding: 0 0.5rem;
        font-family: $SaaSeries;
        font-size: 0.75rem;
        z-index: 2;
        width: fit-content;
    }

    input {
        color: $primary-blue;
        background: none;
        border: 1px solid $primary-blue;
        border-radius: 4px;
        margin-top: -2rem;
        padding: 1.2rem 0 1rem 1.5rem;
        font-family: $ITCClearface;
        font-size: 1.25rem;
        width: 100%;
        z-index: 1;

        &:focus {
            background: none;
        }

        &::-webkit-search-cancel-button{
            margin-right: 1rem;
        }
    }
}

.search-results {
    &__block {
        width: 270px;
        margin: 1.5rem 0;
        @include media-breakpoint-up(lg) {
            margin-right: 0.6rem;
            &::nth-child(3) {
                margin-right: 0;
            }
        }
        @include media-breakpoint-up(xxl) {
            width: 330px;
            margin-right: 0.6rem;
        }
    }
    
    .card-basic {
        @include media-breakpoint-up(md) {
            margin-right: 0.4rem;
        }

        .image-product {
            object-fit: contain;
        }

        .watermark, .watermark > svg {
            width: 100%;
            height: 100%;
        }

        &__header {
            text-transform: capitalize;
        }
    }

    &__empty {
        font-family: $ITCClearface;
        font-size: 1rem;
        @include media-breakpoint-up(md) {
            @include header-3;
        }
    }
    
    &__grid {
        @include media-breakpoint-up(lg) {
            &::after {
                content: "";
                width: 30%;
            }
        }
        @include media-breakpoint-up(xl) {
            &::after {
                content: "";
                width: 26%;
            }
        }
    }
}