.single-article {
    padding-top: 3rem;
    @include media-breakpoint-up(lg) {
        padding-top: 0;
    }
    
    .back-button {
        text-decoration: none;
        font-size: 0.875rem;
        @include media-breakpoint-up(md) {
            width: fit-content;
            margin: 0 auto;
        }
        @include media-breakpoint-up(lg) {
            position: sticky;
            margin-left: 0;
            top: 90%;
        }

        svg {
            width: 3.25rem;
            height: 3.25rem;
            transform: translateX(0);
            transform-origin: center;
            transition-duration: 0.3s;
        }

        &:hover {
            svg {
                transform: translateX(-0.25rem);
            }
        }
    }

    &__header {
        @include media-breakpoint-up(lg) {
            text-align: center;
        }
    }
    
    &__tag {
        padding: 0.3rem 1.1rem;
        margin-right: 0.4rem;
        border-radius: 2rem;
        text-decoration: none;
        background: none;
        border: 1px solid $primary-blue;
        transition: all 0.3s;
        a{
            text-decoration: none;
            color: $primary-blue;
        }
        &:hover{
            transition: all 0.2s;
            background-color: $primary-blue;
            a{
                color:$white;

            }
        }
        @include media-breakpoint-up(lg) {
            margin: 0 0.375rem;
        }
    }

    &__excerpt {
        max-width: 30ch;
        @include media-breakpoint-up(md) {
            max-width: 70ch;
            text-align: center;
        }
    }

    &__image {
        width: 100%;
        img {
            width: 100%;
            height: auto;
            max-height: 210px;
            object-fit: cover;
            margin: 2rem auto;
            @include media-breakpoint-up(lg) {
                max-height: 420px;
            }
        }
    }
    
    .rte {
        @include media-breakpoint-up(lg) {
            // display: flex;
            // flex-direction: column;
            // align-items: center;
            // text-align: center;
            padding-top: 1rem;
        }
        
        h2 {
            @include header-2;
        }
        
        ul {
            @include media-breakpoint-up(lg) {
                max-width: 60ch;
            }
        }
    }
}