.section-image-gallery {
    padding-top: 3rem;
    padding-bottom: 6rem;
    position: relative;
    @include media-breakpoint-up(lg) {
        padding-top: 6rem;
    }

    h3 {
        color: $secondary-white;
        font-size: toRem(52);
        padding-bottom: 2rem;
    }

    figcaption {
        background: rgba(49, 110, 158, 0.86);
        padding: toRem(17) 1rem 0 toRem(13);
        position: absolute;
        bottom: 0;
        left: 0;


        @include media-breakpoint-up(lg) {
            transform: translateY(100%);
            transition: transform ease 0.2s;
        }

        h4,
        p {
            color: $secondary-white;
        }
    }

    figure {
        height: 380px;
        width: 100%;
        overflow: hidden;
        position: relative;
        @include media-breakpoint-up(lg) {
            height: 500px;
        }

        img {
            object-fit: cover;
            height: 100%;
            width: 100%;
        }
    }

    .image-gallery {
        figure {
            @include media-breakpoint-up(lg) {
                &:hover {
                    figcaption {
                        transform: translateY(0%);
                    }
                }
            }
        }
    }

    .gallery-image-slide {
        @include media-breakpoint-up(lg) {
            padding-bottom: 2rem;
            width: 62.5%;
            &:nth-child(2) {
                width: 32.5%;
            }
            &:nth-child(3) {
                width: 32.5%;
            }
            &:nth-child(6) {
                width: 32.5%;
            }
            &:nth-child(7) {
                width: 32.5%;
            }
        }
    }
    .read-more {
        &__text {
            font-family: $SaaSeries;
        }

        &__arrow {
            margin-top: 0.225rem;
            margin-left: 0.95rem;
            width: 0.85rem;
            margin-right: 0.5rem;
        }
    }

    .image-gallery-holder {
        position: relative;
        z-index: 5;
    }

    .background-image {
        position: absolute;
        bottom: -16rem;
        right: -20rem;
        z-index: 1;
    }
}