/* != Footer
-------------------------------------------------------------- */
.main-footer {
    padding-bottom: 5rem;
    background: $primary-blue;
    @include media-breakpoint-up(md) {
        padding-bottom: 0;
    }
}

.newsletter {
    position: relative;
    margin-bottom: 6.25rem;
    padding: 4.5rem 2rem 2.5rem 2rem;
    @include media-breakpoint-up(md) {
        height: 100%;
        margin-bottom: 0;
        padding: 6rem 2rem 14.5rem 2rem;
    }
    @include media-breakpoint-up(lg) {
        padding: 6rem 4rem 4rem 4rem;
    }
    @include media-breakpoint-up(xl) {
        padding: 6.75rem 4rem 6rem 4rem;
    }

    &__header {
        @include media-breakpoint-up(lg) {
            font-size: 4.25rem;
        }
    }

    &__subheader {
        @include media-breakpoint-up(xl) {
            max-width: 30ch;
        }
    }

    .klaviyo-form {
        padding: 0 !important;
        
        .needsclick[data-testid="form-component"] {
            padding-left: 0 !important;
            padding-right: 0 !important;
        }

        input {
            border: none !important;
            border-radius: 0 !important;
            border-bottom: 1px solid $secondary-gold !important;
            color: $secondary-gold !important;
            font-family: $EphemeraEgyptianMedium !important;
            font-size: $font-size-body-1 !important;

            &::placeholder {
                color: $secondary-gold !important;
                font-family: $EphemeraEgyptianMedium !important;
                font-size: $font-size-body-1 !important;
            }
        }

        input[type="email"] {
            padding-left: 0 !important;
            background: none !important;
        }

        button {
            border-radius: 0 !important;
            background: none !important;
            border-bottom: 1px solid $secondary-gold !important;
            color: transparent !important;
            &::after {
                content: "";
                background: url("https://cdn.shopify.com/s/files/1/0688/3596/7262/files/icon-arrow.svg?v=1681337939") no-repeat;
                position: absolute;
                right: 0;
                top: 1.85rem;
                width: 26px;
                height: 26px;
            }
        }

    }

    .links-social {
        a {
            text-decoration: none;
            margin-right: 0.5rem;
            @include media-breakpoint-up(lg) {
                margin-right: 1rem;
            }
            
            svg {
                width: 3rem;
                height: 2.3rem;
                @include media-breakpoint-up(md) {
                    width: 1.5rem;
                    height: 1.5rem;
                }
                @include media-breakpoint-up(lg) {
                    width: 2.75rem;
                    height: 2.75rem;

                    path {
                        transition: fill 0.3s;
                    }
                    
                    &:hover {
                        path {
                            fill: $secondary-gold;
                        }
                    }
                }
            }
        }
    }
}

.footer-nav {
    padding-right: calc(var(--bs-gutter-x) * .5);
    padding-left: calc(var(--bs-gutter-x) * .5);
    @include media-breakpoint-down(md) {
        margin: 0 auto;
        max-width: 90%;
    }
    @include media-breakpoint-up(md) {
        padding-top: 3rem;
        padding-bottom: 4rem;
    }
    @include media-breakpoint-up(lg) {
        padding-top: 6rem;
        padding-bottom: 0;
    }

    .links {
        &-main, &-utility {
            .category {
                list-style: none;
                margin-bottom: 2.5rem;
                @include media-breakpoint-up(lg) {
                    padding-right: 1rem;
                    line-height: 1.2;
                }
    
                &-item {
                    list-style: none;
                    margin-bottom: 1.625rem;
                    @include media-breakpoint-up(lg) {
                        margin-bottom: 1.5rem;
                    }
    
                    a {
                        position: relative;
                        font-size: 1rem;
                        text-decoration: none;
                        transition-duration: 0.3s;

                        @include media-breakpoint-up(lg) {
                            &::before {
                                content: "";
                                position: absolute;
                                bottom: -5px;
                                left: 0;
                                width: 100%;
                                height: 2px;
                                background: $secondary-white;
                                opacity: 0;
                                transform: scaleX(0);
                                transform-origin: 0 50%;
                                transition: all 0.3s;
                            }
                            &:hover {
                                &::before {
                                    opacity: 1;
                                    transform: scaleX(100%);
                                }
                            }
                        }
                    }
                }
            }
       }

        &-utility {
            .category-item {
                line-height: 1.3;
                @include media-breakpoint-up(lg) {
                    font-size: 0.875rem;
                }
            }

            li {
                width: fit-content;
                min-width: 50%;
                list-style: none;
                line-height: 1.3;
                @include media-breakpoint-up(lg) {
                    min-width: 0;
                    margin-right: 1.25rem;
                    letter-spacing: 0;
                    font-size: 0.875rem;
                    
                    &:last-of-type {
                        margin-right: 0;
                    }
                }
                @include media-breakpoint-up(xl) {
                    margin-right: 1.75rem;
                }

                a {
                    line-height: 1.3;
                    font-family: $SaaSeries;
                    color: $secondary-white;
                    transition: color 0.3s;
                    text-decoration: none;
                    @include media-breakpoint-up(lg) {
                        font-size: 0.875rem;
                        text-decoration: underline;
                    }
                    &:hover {
                        color: $secondary-gold;
                    }
                }
            }

            .linklist {
                padding: 0;
                @include media-breakpoint-up(lg) {
                    padding-right: 1rem;
                }
                @include media-breakpoint-up(xl) {
                    padding-right: 2rem;
                }
            }
        }
    }
}
