.heading-basic {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: $primary-blue;
    margin-bottom: 1.5rem;
    @include media-breakpoint-up(md) {
        margin-bottom: 3rem;
    }

    h2 {
        letter-spacing: 0;
        line-height: 1;
    }

    .button-container {
        display: flex;
        align-items: center;
        gap: 1rem;
    }
}