.related-content,.basic-card-swiper {
    padding-top: 3rem;
    padding-bottom: 3rem;
    @include media-breakpoint-up(lg) {
        padding-top: 6rem;
        padding-bottom: 6rem;
    }
    
    .button-container {
        margin-bottom: 3.5rem;
    }

    &.theme--dark{
        background: $primary-blue;
        color:$white;
    }
    
    .swiper-wrapper {
        @include media-breakpoint-up(lg) {
            &::after {
                content: "";
                width: 330px;
            }
        }
    }

    .card-basic {
        .image-wrap {
            @include media-breakpoint-up(lg) {
                width: 100%;
            }
        }
    }
}