// CTA Buttons
.btn {
  font-family: $SaaSeries;
  border-radius: 3.5rem;
  font-size: toRem(14);
  letter-spacing: -0.01rem; // Equals -10 in milli ems from XD units
  width: fit-content;
  height: fit-content;
  max-width: 320px;
  padding: 0.6rem 1.2rem 0.5rem;
  border-width: 0;
  @include media-breakpoint-up(md) {
    font-size: toRem(21);
    max-width: none;
    padding: 0.65rem 1.875rem 0.575rem;
  }

  &-lg {
      min-width: toRem(214);
      font-size:toRem(21)
  }

  &-sm {  
    font-size: toRem(12);
    max-width: 220px;
    padding: 0.45rem 1rem 0.375rem;
  }

  &-play {
    background: $secondary-white;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    border: none;
    @include media-breakpoint-up(md) {
      width: 5.25rem;
      height: 5.25rem;
    }

    svg {
      width: 21px;
      height: 22px;
      margin-left: 6px;
      @include media-breakpoint-up(md) {
          width: 29px;
          height: 29px;
      }
    }
  }

  &-close {
    background: $secondary-white;
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    padding: 0;
    z-index: 1;
    font-size: 1.25rem;
    @include media-breakpoint-up(md) {
        width: 3.25rem;
        height: 3.25rem;
    }
  }
}

