.btn-primary {
  background: $primary-blue;
  color: $secondary-white;
  border: none;
  transition: background 0.3s;
  &:hover, &:focus, &:focus-visible {
    background: $secondary-gold;
  }
}

.btn-secondary {
  background: none;
  color: $primary-blue;
  border: 2px solid $primary-blue;
  transition: all 0.3s;
  &:hover, &:focus, &:focus-visible {
    background: $primary-blue;
    color: $secondary-white;
  }
}

.btn-tertiary {
  font-family: $SaaSeries;
  color: $primary-blue;
  transition: 0.3s color;
  &:hover, &:focus, &:focus-visible {
    color: $secondary-gold;
  }
}


.btn-light {
  color: $primary-blue;
  background: $secondary-white;
  border: none;
  transition: all 0.3s;
  &:hover {
    background: $secondary-gold;
    color: $secondary-white;
  }
}

.btn-swatch {
  margin-right: 0.5rem;
  
  label {
    @include header-6;
    padding: 0.4rem 1rem;
    border-radius: 1rem;
    border: 1px solid $primary-blue;
    cursor: pointer;
  }

  // &:hover, &:focus, &:focus-visible {
  //   background: $secondary-gold;
  // }
}

.swatch-element {
  cursor:pointer;

  input{
      display: none;
  }

  &:has( input:checked ){
      label{
          background: $primary-blue;
          color:$white;
      }
  }  
  &.soldout{
      opacity: .4;
      pointer-events: none;
  }
}

.product-qty {
  display: flex;
  align-items: center;
  width: fit-content;

  input {
      background:none;
      text-align: center;
      width: 2.3rem;
      height: 2rem;
      display: inline-flex;
      margin: auto 1rem;
      padding-top: 0.5rem;
      border: none;
      @include body-1;
  }

  a {
      display: inline-flex;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 2rem;
      height: 2rem;
      color:$primary-blue;
      border-radius: 100%;
      border: 1px solid $primary-blue;
      text-decoration: none;
      @include body-1;
  }
}

.btn-outline-light {
  @include button-outline-variant($secondary-white, #222222, lighten($secondary-white, 5%), $secondary-white);
  color: $secondary-white;
  border-width: 3px;

  &:hover {
      color: $primary-blue;
  }
}

.btn-outline-primary {
  @include button-outline-variant($primary-blue, #222222, lighten($primary-blue, 5%), $primary-blue);
  border-width: 3px;
  transition: all 0.3s;
  &:hover {
    border-color: $secondary-gold;
    background: $secondary-gold;
    color: $secondary-white;
  }
}

.btn-variant {
  @include button-outline-variant($primary-blue, #222222, lighten($primary-blue, 5%), $primary-blue);
  border-width: 3px;
  transition: all 0.3s;
  &:hover {
    border-color: $primary-blue;
    background: $primary-blue;
    color: $secondary-white;
  }
}

.variant-active {
  border-color: $primary-blue;
  background: $primary-blue;
  color: $secondary-white;
}

// .btn-secondary {
//   @include button-variant($secondary-gold, darken($secondary-gold, 7.5%), $secondary-white, lighten($secondary-gold, 5%), lighten($secondary-gold, 10%), $secondary-white);
// }

.btn-outline-secondary {
  @include button-outline-variant($secondary-gold, #222222, lighten($secondary-gold, 5%), $secondary-gold);
  border-width: 3px;

  &:hover {
      color: $secondary-white;
  }
}


// Swiper
.swiper-button-next,
.swiper-button-prev {
  position: relative; 
  margin-top: 0;  
  width: toRem(55);
  height: toRem(55);
  min-width: toRem(55);
  min-height: toRem(55);
  padding: 0;
  background: transparent;
  border: none;
  transition: all 0.3s;
  cursor: pointer;
  
  // @include media-breakpoint-up(md) {
  //   width: toRem(72);
  //   height: toRem(72);
  // }

  svg {
    width: 100%;
    height: 100%;
    path { transition-duration: 0.3s; }
    circle { transition-duration: 0.3s; }
  }

  &:hover {
    .icon-next {
      path { fill: $secondary-white; }
      circle { stroke: $primary-blue; fill: $primary-blue; }
    }
  }

  // &.dark {
  //   &:hover {
  //     .next-white-fill, .next-blue-fill {
  //       fill: $secondary-gold;
  //     }
  //   }
  // }

  &.swiper-button-disabled {
    cursor: default;
    opacity: 0.5;
    pointer-events: none;
    
    &:hover {
      .next-white-fill, .next-blue-fill {
        fill: inherit;
      }
    }
  }
  
  &.swiper-button-lock {
    display: none;
  }
}

.swiper-button-prev {
  svg {
    transform: scaleX(-1);
  }
}
/** 
Themes for parent elements 
**/
.theme--light,.light{}
.theme--dark, .dark{
  .swiper-button-next,
  .swiper-button-prev{
    .next-blue-fill{
      fill:$primary-dark-grey;
    }
    &:hover{
      .next-blue-fill{
        fill:$secondary-white !important;
      }
    }
  }

}


// Modal
.modal .btn-close {
  position: absolute;
  width: toRem(54);
  height: toRem(54);
  opacity: 1;
  border-radius: 50%;
  background: $secondary-white;
  color: $primary-blue;
  border: 2px solid $primary-blue;
  box-shadow: 0 1rem 2rem $primary-blue;
  z-index: 10;  
  @include media-breakpoint-up(lg) {
    transition-duration: 0.3s;
    &:hover {
        opacity: 0.7;
    }
  } 

  svg {
    margin-top: -3px;
  }
}

.modal .swiper-button {  
  @include media-breakpoint-up(lg) {
    transition-duration: 0.3s;
    &:hover {
      svg > g {
        circle { fill: $secondary-gold; stroke-width: 0; }
        path { fill: $secondary-white; }
      }
    }
  } 
}