.blog-faqs {
    padding-bottom: 3rem;
    @include media-breakpoint-up(lg) {
        padding-bottom: 7rem;
    }

    .faq-container {
        padding-right: calc(var(--bs-gutter-x) * 1.5);
        padding-left: calc(var(--bs-gutter-x) * 1.5);

        @include media-breakpoint-up(lg) {
            padding-left: 8.3333%;
            padding-right: 8.3333%;
        }
    }

    .heading {
        padding-left: calc(var(--bs-gutter-x) * 1.5);
        @include media-breakpoint-up(lg) {
            padding-left: inherit;
        }
    }

    &__header {
        margin-top: 3rem;
        @include media-breakpoint-up(lg) {
            margin-bottom: 4rem;
        }
    }

    #filter-button {
        margin-top: 1rem;
        margin-bottom: 2rem;
    }

    .accordion {
        color: $primary-blue;

        &-item {
            &:first-child {
                border-top: none;
            }
        }

        .accordion-header {
            .accordion-button {
                flex-wrap: wrap;
                align-items: flex-start;
            }
        }

        .accordion-body {
            padding-bottom: 2rem;
            @include media-breakpoint-up(lg) {
                padding-bottom: 3rem;
            }
        }

        &-tags {
            cursor: default;

            .tag {
                color: $primary-blue;
                text-decoration: underline;
            }
        }
    }

    .filter-header {
        font-family: $EphemeraEgyptianMedium;
        font-size: 1.875rem;
        margin-bottom: 2rem;
    }

    .faq-categories-filter {
        padding-left: 0;
        @include media-breakpoint-up(lg) {
            padding-bottom: 7rem;
        }

        li {
            list-style: none;
            position: relative;
            width: fit-content;

            a {
                text-decoration: none;
                color: $secondary-white;
                &:hover, &.active {
                    color: $secondary-gold;
                }
            }
        }

        .has-children {
            &--link {
                padding-right: 0.5rem;
                margin-bottom: 2rem;
                transition-duration: 0.3s;
                &:hover {
                    color: $secondary-gold;
                }
            }

            .list-child {
                color: $secondary-white;
                padding-left: 1.5rem;
                position: relative;

                &:has(.active) {
                    &::before {
                        content: "";
                        position: absolute;
                        top: 0;
                        left: -3px;
                        width: 6px;
                        height: 100%;
                        background: $secondary-gold;
                    }
                }
            } 

            .level-2 {
                border-left: 1px solid $secondary-white;
                @include media-breakpoint-up(lg) {
                    padding-left: 0;

                    li {
                        padding-left: 2rem;
                    }
                }
            }

            a[aria-expanded="true"] {
                & + svg {
                    transform: scaleX(-1) rotate(180deg);

                    .cls-1 {
                        fill: $secondary-gold;
                    }
                }
            }
        }
    }
}

#faq-categories-filter{
    ul,li{ list-style: none;}
    a{
        text-decoration: none;
        &.active{
            text-decoration: underline;
        }
    }
}