.product-section-template {
    background: $primary-light-grey;
    color: $primary-blue;
    padding: 2rem 0 7rem;
    position: relative;
    overflow: hidden;
    @include media-breakpoint-up(lg) {
        padding-bottom: 0;
        margin-bottom: 7rem;
    }

    .product-single {
        &__images {
            position: relative;
            margin-bottom: 2rem;

            .swiper-product-main {
                overflow-x: hidden;
                @include media-breakpoint-up(md) {
                    padding-top: 0;
                }
                
                .swiper-wrapper {
                    .swiper-slide {
                        &__image {
                            width: 100%;
                            height: 300px;
                            object-fit: contain;
                            @include media-breakpoint-up(lg) {
                                width: 100%;
                                height: 480px;
                            }
                        }

                        &__video {
                            height: auto;
                            align-items: center;

                            video {
                                width: 100%;
                                object-fit: contain;
                            }
                        }
                    }
                }
            }

            .thumbs-wrap {
                height: 82px;
            }

            .swiper-product-thumbs {
                overflow-x: clip;
                overflow-y: visible;
                padding: 0 1rem;
                max-width: calc(100% - 112px);

                .swiper-wrapper {
                    display: flex;
                    align-items: center;
                    
                    .swiper-slide {
                        width: 70px;
                        height: 90px;
                        transition: width, height 0.3s;
                        cursor: pointer;
                        @include media-breakpoint-up(lg) {
                            width: 70px;
                            height: 90px;
                        }
                        
                        &__image {
                            width: 100%;
                            height: 100%;
                            object-fit: contain;
                        }

                        &:not(.swiper-slide-active) {
                            width: 40px;
                            height: 55px;
                            @include media-breakpoint-up(lg) {
                                width: 50px;
                                height: 60px;
                            }

                            .swiper-slide__image {
                                object-fit: cover;
                            }
                        }

                        &--video {
                            overflow: hidden;

                            img {
                                width: 100%;
                                height: 100%;
                                object-fit: contain;
                            }
                        }

                        &-active {
                            &.swiper-slide--video {
                                border: none;
                            }

                            border: 2px solid $primary-blue;
                            .custom {
                                object-fit: contain;
                            }
                        }
                    }
                }
            }

            .swiper-button-next, .swiper-button-prev {
                flex: 15%;
                width: 2rem;
                height: 2rem;
                @include media-breakpoint-up(lg) {
                    width: 2.5rem;
                    height: 2.5rem;
                }
            }

            .swiper-button-prev {
                margin-right: 0.5rem;
                @include media-breakpoint-up(lg) {
                    margin-right: 1rem;
                }
            }

            .swiper-button-next {
                margin-left: 0.5rem;
                @include media-breakpoint-up(lg) {
                    margin-left: 1rem;
                }
            }
        }
    }

    .content-container {
        display: flex;
        flex-direction: column;
    }

    .product-single__details {
        z-index: 1;
    }
    
    .product-title {
        @include media-breakpoint-up(md) {
            font-size: toRem(42);
            line-height: 1.1;
        }
    }

    .product-form{
        .price__pricing-group {
            .price-item{
                @extend h4
            }
        }
        .swatch-container-label{
            @extend .h5;
            margin-bottom:1rem;
            margin-top:1rem;
            display: inline-block;
        }
        .swatch-element-container{
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 0;   
        }

        &__footer {
            .btn-primary, .btn-fish-calc {
                font-size: toRem(14);
                max-width: 320px;
                padding: 0.6rem 1.2rem 0.5rem;
            }
        }
    }

    .btn-fish-calculator {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        right: 0;
        bottom: 0;
        width: 220px;
        border: 3px solid $primary-blue;
        border-right: none;
        border-radius: 10rem;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        color: $primary-blue;
        z-index: 2;
        @include media-breakpoint-up(xl) {
            @include button-outline-variant($primary-blue, #222222, lighten($primary-blue, 5%), $primary-blue);
            padding: 2.75rem 1.5rem;
            background: transparent;
            border-right: none;

            &:hover {
                background: $primary-blue;
                color: $secondary-white;
            }

        }

        .icon-holder {
            width: 2rem;
            display: none;
            @include media-breakpoint-up(xl) {
                display: block;
            }

            svg {
                width: 100%;
            }
        }

        span {
            font-size: toRem(18);
            
            @include media-breakpoint-up(xl) {
                font-size: toRem(14);
                width: 15ch;
            }
        }

        @media (hover:hover) {
            &:hover {
                .icon-holder {
                    svg {
                        fill: $secondary-white;
                    }
                }
            }
        }

    }
}

// Mini Nav
.mini-nav {
    justify-content: flex-end;
    padding-left: 1.5rem;
    padding-bottom: 0.5rem;

    &__link {
        position: relative;
        font-size: 0.875rem;
        color: $primary-blue;
        text-decoration: none;
        transition: all 0.3s;

        &::before {
            content: "";
            position: absolute;
            top: 3px;
            left: -8px;
            width: 2px;
            height: 70%;
            background-color: $secondary-gold;
            opacity: 0;
            transform-origin: center;
            transform: scaleY(0);
            transition: all 0.4s;
        }
        
        &:hover, &.active {
            color: $secondary-gold;
            &::before {
                opacity: 1;
                transform: scaleY(1);
            }
        }
    }
}

.gsap-mininav {
    position: relative;

    .mini-nav__anchor {
        position: absolute;
        top: -10rem;
        left: 0;
    }
}

#mini-nav {
    a {
        scroll-behavior: smooth;
    }
}

.mini-nav-sticky {
    position: sticky;
    bottom: 1.5rem;
    z-index: 0;
    width: 10rem;
}

@media (min-width: 1600px) {
    .faqs-section {
        .laptop {
            margin-left: 0;
        }

        .btn-primary {
            margin-left: 41.6667%;
        }

        .accordion {
            width: 58.3333%;
        }
    }

    .media-gallery {
        .laptop {
            margin-left: 0;
            width: 100%;
        }
    }

    .product-section-details__inner {
        .section-image {
            width: 50%;
        }
    }

    .product-section-related-content, .product-section-recommendations {
        .laptop {
            width: 30%;
        }
        .header-wrap {
            margin-left: 0;
        }
    }

    .swiper-laptop {
        margin-left: 0;
    }

    .row-laptop {
        justify-content: space-between;
    }

    .product-section-related-content .swiper {
        padding-right: 0;
    }
}
