.two-column-swiper {
    padding-top: 3rem;
    padding-bottom: 3rem;
    @include media-breakpoint-up(lg) {
        padding-top: 5rem;
    }

    .container {
        position: relative;
    }
    
    &__lead {
        @include media-breakpoint-up(lg) {
            font-size: toRem(22);
        }
    }

    .image-wrap {
        position: relative;
        height: 290px;
        order: -1;
        @include media-breakpoint-up(lg) {
            height: 500px;
        }
    }
        
    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
        @include media-breakpoint-up(lg) {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
        }

        &.contain {
            object-fit: contain;
        }
    }

    .content {
        &__body {
            p {
                margin-bottom: 0;
            }
        }

        &__url {
            letter-spacing: -0.1px;
            font-size: 0.875rem;
            width: fit-content;
        }
    }
    &.theme--light{

    }

    &.theme--dark{
        background: $primary-blue;
        @include media-breakpoint-up(lg) {
            padding-top: 6rem;
        }

        p, h1, h2, h3, h4, h5{
            color:$white;
        }
    }

    .swiper {
        padding-right: 0;

        &-slide {
            width: 100%;
        }
    }
}
