.cart-template {
    background: none;
	padding-top: 4rem;
	@include media-breakpoint-up(md) {
		padding-top: 0;
	}

    .product-form__qty {
        display: flex;
        justify-content: start;
        width: fit-content;

        &-dec {
            font-size: 1.375rem;
        }

        &-inc {
            font-size: 1.375rem;
        }

        .clickable {
            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid $primary-blue;
            border-radius: 50%;
            height: 2rem;
        }

        .product-form__qty-input {
            font-family: $ITCClearface;
            font-size: 1rem;
            border: none;
			background: none;
        }

        a {
            text-decoration: none;
            color: $primary-blue;
        }
    }

	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
    
	&__line_items{
		max-height: calc(80vh - 150px);
		overflow: auto;
	}
	.line-item {
		margin: 0;
		padding: 2em 1em;
		button{
			color: white;
			background: transparent;
			width: 1.25em;
			padding: 0;
			height: 1.75em;
			border: none;
			margin-right: 0;
			&.remove{
				margin-left: 1em;
				fill: $white;
				&:hover {
					color: $red;
				}
			}
		}
		&__quantity {
			float:left;
			button{
			}
			input[type='text']{
				background-color: transparent;
				color: white;
				border-left: 0px;
				border-right: 0px;
				width: 2em;
				height: 1.75em;
				padding-left: .25em;
				margin-right: 0;
			}
		}
		&__variant {
			font-size: $font-size-sm;
			display:inline-block;
			width: 100%;
			color: $gray-200;
		}
		&__price {
			float: right;
		}
		&__image{
			background:white;
			display: flex;
			justify-content: center;
			align-items: center;
			padding:5px;
			img{
				max-height:70px;
				max-width: 100%;
			}
		}
	}
	&__footer{
		background: $primary;
		position: absolute;
		bottom: 0;
		left: 15px;
		right: 15px;
		min-height: 130px;
		padding-bottom: 30px;
		.subtotal{
			display: flex;
			justify-content: space-between;
			padding-top: 1em;
		}
		p, label{
			margin-bottom:0px;
			font-size:.8rem;
		}
	}

	.cart__quantity-td {
		max-width: 340px;
	}

	.animation-close {
		.leftright, .rightleft {
			width: 20px;
		}
	}
}


.cart-drawer {
    position: fixed;
    overflow-y: auto;
    overflow-x: hidden;
    top: 1rem;
    bottom: 1rem;
    right: 0;
	left: 0;
    width: 95%;
    padding-left: 1rem;
    padding-right: 1rem;
    margin: 0 auto;
    border-radius: 1rem;
    transform: translateX(105%);
    transition: transform 0.3s;
    transition-timing-function: ease-in-out;
    background: $white;
    z-index: 1000;
    @include media-breakpoint-up(md){
        width: 65%;
        transform: translateX(135%);
        margin: 0;
        left: inherit;
    }
    @include media-breakpoint-up(lg){
        transform: translateX(105%);
        width: 650px;
        top: 0;
        bottom: 0;
        margin: 1rem;
    }

	&:has(.cart-template) {
		padding-top: 0;
	}

    &.open {
        transform: translateX(0);
    }

    .btn-close {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		right: 1.5rem;
        top: 1.5rem;
		width: 3rem;
		height: 3rem;
        opacity: 1;
        transition-duration: 0.3s;
        border: 2px solid $primary-blue;
		cursor: pointer;

        &:hover {
            opacity: 0.7;
        }

        svg {
            margin-top: -2px;
        }
	}
}