.split-hero {
    position: relative;
    padding-bottom: 1rem;
    margin-top: -5rem;
    overflow: hidden;
    background: $primary-blue;
    color: $secondary-white;

    &.section-height {
        &--small {
            min-height: 600px;
        }

        &--medium {
            min-height: 800px;
        }

        &--large {
            min-height: 1000px;
        }
    }

    @include media-breakpoint-up(md) {
        max-height: 950px;
        margin-top: -7rem;
        height: 103vh;
    }
    @include media-breakpoint-up(xxl) {
        max-height: 1150px;
    }

    .container-main,
    .row-main {
        height: 100%;
    }

    .row-main {
        align-items: center;
    }

    &__col-content {
        padding-top: 2rem;
        padding-bottom: 3rem;
        @include media-breakpoint-up(md) {
            padding-top: 0;
            padding-bottom: 0;
        }
    }

    &__col-image {
        height: 112vw;
        @include media-breakpoint-up(sm) {
            height: 500px;
        }
        @include media-breakpoint-up(md) {
            height: 100%;
        }
    }

    &__image {
        position: absolute;
        right: 0;
        top: 0;
        z-index: 0;
        height: 112vw;
        width: 100%;
        @include media-breakpoint-up(sm) {
            height: 500px;
        }
        @include media-breakpoint-up(md) {
            height: 100%;
            width: 45%;
        }
        @include media-breakpoint-up(lg) {
            width: 60%;
        }
        @include media-breakpoint-up(xxl) {
            width: 55%;
        }
        
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }
    }

    &__label {
        margin-bottom: 1.5rem;
        @include media-breakpoint-up(md) {
            margin-bottom: 2rem;
            font-size: toRem(17);
        }
    }
    
    &__header {
        position: relative;
        margin-bottom: .75rem;
        z-index: 1;
        @include media-breakpoint-up(md) {
            margin-bottom: 3rem;
        }
    }

    &__body {
        p {
            margin-bottom: 0;
        }
    }

    &.image-position--left {
        .split-hero__image {
            left: 0;
            right: initial;
        }
    }

    // Theme Colors 
    &.theme--light {
        color: $primary-blue;
        background: $primary-light-grey;
    }
    &.theme--dark{
        color: $secondary-white;
        background: $primary-blue;
    }
}
@include media-breakpoint-down( md ){
    body:has( .navbar.opaque ) {
        .split-hero__image {
            top:32px !important;
            height:100vw !important;
        }
    }
}