.single-recipe {
    margin-top: 4rem;
    @include media-breakpoint-up(md) {
        margin-top: 7rem;
    }
    
    &.intro {
        position: relative;
        @include media-breakpoint-up(md) {
            margin-bottom: 5rem;
        }
    }
    
    &.instructions {
        margin-top: 0;
        padding-top: 0;
        position: relative;
        background: $secondary-white;
        @include media-breakpoint-up(md) {
            position: sticky;
            top: 0;
            left: 0;
            width: 110%;
            margin-left: -100%;
            margin-top: -10rem;
            padding-top: 15rem;
            z-index: 0;
        }
        @include media-breakpoint-up(lg) {
            width: 100%;
        }
        @media (min-width: 1600px) {
            margin-left: -17%;
        }
        // @include media-breakpoint-up(md) {
        //     padding-top: 16rem;
        //     position: absolute;
        //     top: 15rem;
        //     left: 0;
        //     z-index: -1;
        //     width: 45%;
        // }
        // @include media-breakpoint-up(lg) {
        //     width: 33%;
        // }
        // @media (min-width: 2000px) {
        //     max-width: 1660px;
        //     margin-left: auto;
        //     margin-right: auto;
        // }
    }

    .image-wrap {
        position: relative;

        &::after {
            content: "";
            position: absolute;
            top: 4rem;
            left: -5%;
            width: 102%;
            height: 87%;
            // background: $secondary-white;
            z-index: -1;
            @include media-breakpoint-up(md) {
                display: none;
            }
        }
        
        &__image {
            width: 100%;
            height: auto;
            object-fit: cover;
            position: relative;
            z-index: 1;
            @include media-breakpoint-up(md) {
                min-height: 350px;
                max-height: 500px;
            }
        }

        &__info {
            position: relative;
            z-index: 1;
            .item {
                &__label {
                    font-size: 0.75rem;
                    text-transform: uppercase;
                }

                &__value {
                    font-size: $font-size-body-1;

                    span {
                        font-size: 0.75rem;
                        text-transform: uppercase;
                    }
                }
            }
        }
    }

    &__content {
        font-size: $font-size-body-1;

        .share-this {
            p {
                font-size: 0.875rem;
            }
        }
    }

    &__ingredients, &__directions {
        h2 {
            font-size: toRem(32);
            @include media-breakpoint-up(lg) {
                @include header-2;
            }
        }

        a {
            color: $tertiary-gold;
        }
        p:last-of-type {
            border-bottom: none;
        }
    }

    &__ingredients {
        background: $secondary-white;
        padding-right: calc(var(--bs-gutter-x) * 1.5);
        padding-left: calc(var(--bs-gutter-x) * 1.5);
        @include media-breakpoint-up(md) {
            display: flex;
            flex-direction: column;
            padding-bottom: 1.5rem;
            padding-left: 3rem;
        }    
        @include media-breakpoint-up(xl) {
            padding-left: 6rem;
        }    

        // h2 {
        //     @include media-breakpoint-up(md) {
        //         // margin-left: -1rem;
        //     }
        // }
        
        h3 {
            @include body-1;
            margin-top: 2.5rem;
        }

        ul {
            margin-top: 1.5rem;
            margin-bottom: 3rem;
            padding-left: 0;
            
            li {
                font-size: 1.125rem;
                line-height: 2.25;
                list-style: none;
                padding-left: 2rem;
                position: relative;

                span.arrow {
