                    height: 0.875rem;
                }
            }
        }
    }

    &__featured-products {
        padding-bottom: 3rem;
        .header {
            @include header-4;
            margin-bottom: 2rem;
            @include media-breakpoint-up(md) {
                margin-bottom: 1rem;
            }
        }
        .featured-product {
            text-decoration: none;
            display: flex;
            align-items: center;
            gap: .5rem;
            color: $primary-blue;
            &:not(:last-child) {
                border-bottom: 1px solid $primary-blue;
                padding-bottom: 2rem;
                margin-bottom: 2rem;
            }
            .image-holder {
                flex-basis: 110px;
                flex-shrink: 0;
                @include media-breakpoint-up(lg) {
                    flex-basis: 150px;
                }
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
            .content-holder {
                .featured-product-title {
                    margin: 0;
                    margin-bottom: .25rem;
                    transition: text-decoration-color .3s;
                    text-decoration: underline rgba($secondary-gold, 0);
                }
                .featured-product-category {
                    margin-bottom: .5rem;
                }
            }
            &:hover {
                .featured-product-title {
                    text-decoration: underline $secondary-gold;
                }
            }
        }
    }

    &__directions {   
        background: none;
        margin-top: 3rem;
        padding-bottom: 3rem;
        padding-right: calc(var(--bs-gutter-x) * 1.5);
        padding-left: calc(var(--bs-gutter-x) * 1.5);
        @include media-breakpoint-up(lg) {
            margin-top: 6rem;
        }

        h3 {
            display: flex;
            align-items: center;
            padding-top: 2.5rem;
            margin-bottom: 1.5rem;
            @include body-1;
            @include media-breakpoint-up(md) {
                margin-bottom: 0.875rem;
            }

            span {
                @include header-5;
                margin-right: 1rem;
                @include media-breakpoint-up(md) {
                    margin-top: -0.25rem;
                    margin-right: 2.25rem;
                }
            }
        }
        
        p {
            display: flex;
            flex-direction: column;
            padding-bottom: 2.5rem;
            margin-bottom: 0;
            border-bottom: 1px solid $primary-blue;
            @include body-2;
            @include media-breakpoint-up(md) {
                padding-left: 3.35rem;
                max-width: 45ch;
            }
            @include media-breakpoint-up(xl) {
                max-width: 70ch;
            }

            span {
                margin-top: 2.5rem;
                margin-bottom: 1rem;
                @include header-5;
            }
        }
    }
}


.recipe-media {
    padding-top: 1.5rem;
    padding-bottom: 5rem;
    @include media-breakpoint-up(md) {
        padding-top: 3.5rem;
    }
    @include media-breakpoint-up(lg) {
        padding-bottom: 4rem;
        padding-top: 4rem;
    }
    @include media-breakpoint-up(xl) {
        padding-bottom: 7.5rem;
        padding-top: 6rem;
    }
    
    &__header {
        font-size: 3rem;
        text-align: left;
        @include media-breakpoint-up(lg) {
            font-size: 6rem;
        }
    }

    &__image {
        position: relative;
        height:25vh;
        img {
            height:25vh;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        @include media-breakpoint-up( lg ){
            height:50vh;
            img{
                height: 50vh;
            }
        }

        .btn-play {
            position: absolute;
            z-index: 1;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
}


.recipe-instagram {
    overflow: hidden;
    padding-bottom: 2rem;
    padding-top: 3rem;
    @include media-breakpoint-up(lg) {
        padding-top: 2rem;
    }

    .container {
        @include media-breakpoint-down(md) {
            margin-right: 0;
            max-width: 95%;
        }
    }

    .row {
        @include media-breakpoint-up(md) {
            width: 100%;
            margin: 0 auto;
        }
    }

    // &__header {
    //     line-height: 1;
    //     @include media-breakpoint-up(lg) {
    //         font-size: toRem(80);
    //     }
    //     @include media-breakpoint-up(xl) {
    //         font-size: toRem(96);
    //     }
    // }
    
    // &__handle {
    //     line-height: 1;
    //     @include media-breakpoint-up(lg) {
    //         font-size: toRem(80);
    //     }
    //     @include media-breakpoint-up(xl) {
    //         font-size: toRem(96);
    //     }
    // }

    .button-container {
        padding-bottom: 3.5rem;
    }

    &__swiper {
        width: 100%;
        overflow: hidden;
        padding-bottom: 3rem;
        @include media-breakpoint-down(md) {
            padding-left: calc(var(--bs-gutter-x) * .5);
            padding-right: 2rem;
        }
        @include media-breakpoint-up(lg) {
            padding-bottom: 5rem;
        }
        
        &-wrapper {
            display: flex;
            width: fit-content;
        }

        &-slide {
            position: relative;
            width: 270px;
            height: fit-content;

            .image-wrap {
                height: auto;
                width: 100%;
                position: relative;
                cursor: pointer;

                .icon-ig {
                    position: absolute;
                    bottom: 0.875rem;
                    right: 1rem;
                    width: 2.25rem;
                    height: 2.25rem;
                    background: $primary-blue;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    padding: 0.5rem;
                    border-radius: 50%;
                    z-index: 1;

                    path {
                        fill: $secondary-white;
                    }
                }
                
                .filter {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    opacity: 0;
                    transition: opacity 0.3s;
                    background: $primary-blue;
                    z-index: 1;
                }

                img {
                    width: 100%;
                    height: 270px;
                    object-fit: cover;
                    position: relative; 
                    @include media-breakpoint-up(xxl) {
                        height: 330px;
                    } 
                }
            }

            // &--handle {
            //     font-size: 1.75rem;
            // }

            &--url {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 2;
            }

            @include media-breakpoint-up(lg) {
                &:hover {
                    .image-wrap {
                        .filter {
                            opacity: 0.5;
                        }
                    }
                }
            }
        }
    }
}


.recipe-friends {
    padding-top: 2rem;
    padding-bottom: 2rem;
    @include media-breakpoint-up(lg) {
        padding-top: 3rem;
        padding-bottom: 6rem;
    }

    // &__lead {
    //     font-size: 0.875rem;
    //     @include media-breakpoint-up(md) {
    //         font-size: 1.325rem
    //     }
    // }
    
    // &__header {
    //     font-size: 3rem;
    //     line-height: 1;
    //     @include media-breakpoint-up(md) {
    //         font-size: 6rem;
    //     }
    // }

    &__image {
        height: auto;
        max-height: 500px;
        object-fit: cover;
        @include media-breakpoint-up(lg) {
            max-height: 610px;
        }
    }

    .content {
        &__name {
            font-size: 1.75rem;
        }

        &__title {
            font-size: 0.875rem;
        }

        &__body {
            font-size: $font-size-body-1;
        }

        &__url {
            letter-spacing: -0.1px;
            font-size: 0.875rem;
            width: fit-content;
        }
    }
}

.recipe-related-content.product-section-related-content {
    padding-top: 10rem;
    margin-bottom:4rem;
    @include media-breakpoint-up(lg) {
        padding-bottom: 0;
    }
    .heading-container {
        margin-bottom: 3rem;
    }
    .btn {
        margin-top: .5rem;
        color: $secondary-white;
    }
    .btn-primary {
        margin-top: 0;
        // color: $primary-blue;
    }
}