.product-section-recommendations {    
    padding-top: 3rem;
    @include media-breakpoint-up(lg) {
        margin-bottom: 6rem;
    }

    .swiper-offset {
        padding-left: 0;
        padding-right: 0;
    }

    .swiper-related-products {
        @include media-breakpoint-up(lg) {
            overflow: hidden;
        }
    }

    .card-basic {
        @include media-breakpoint-up(md) {
            width: 270px;
            margin: 0;
        }
        .content {
            width: 100%;
        }
    }

    .heading-basic {
        margin-bottom: 0.75rem;
    }
}