// Access <body> element attributes to handle sticky positioning
body {
    &[data-nav-open] {
        [data-sticky] {
            z-index: unset !important;
        }
    }

    &[data-filter-open] {
        [data-sticky] {
            z-index: 1055;
        }

        .modal-backdrop {
            display: none;
        }
    }

    @include media-breakpoint-down(md) {
        [data-sticky] {

            [mx-collection-filter] {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                align-items: center;
            }

            .filter-options {
                border-bottom: 0;
            }

            .badge-row {
                width: 100%;
                border-top: 1px solid $primary-blue;
            }

            .badge-results {
                justify-content: center;
            }

            &[data-fixed] {
                padding-top: 1rem;
                padding-bottom: 1rem !important;
            }
        }
    }

    [blog-filter-sticky] {
        background-color: $primary-light-grey;
        z-index: 9;

        &[blog-filter-fixed] {
            top: var(--mx-sticky-top);
            left: 0;
            right: 0;
            border-top: 2px solid $primary-blue;
            border-bottom: none !important;
            box-shadow: 0 30px 60px #00000029;
            padding-top: 1rem;
            padding-bottom: 1rem !important;
            padding-left: var(--mx-sticky-left);

            .tag-count {
                margin-bottom: 0 !important;
            }

            &[blog-filter-mobile] {
                padding-top: 1rem !important;
            }
        }
    }

    [data-sticky] {
        position: sticky;
        top: var(--mx-sticky-top);
        z-index: 9;

        &[data-fixed] {
            margin-top: 1rem;
            border-top: 2px solid $primary-blue;
            box-shadow: 0 30px 60px #00000029;
            padding-bottom: 0;

            .filter-options {
                border-bottom: 0;
            }

            .badge-row {
                height: 0;
                overflow: hidden;
                opacity: 0;
            }

            #sortButton {
                margin-top: 0 !important;
            }
        }

        .badge-container {
            li.blank-item {
                flex-basis: 100%;
                height: 0;
                margin: 0;
            }

            li.clear-all,
            li.apply-all {
                margin-bottom: 0;
            }
            li.apply-all {
                .btn{
                    margin-top: .1rem;
                }
            }
        }
    }
}

.section-product-filter {
    color: $primary-blue;
    padding-bottom: 1.5rem;
    transition: color 0.2s ease, background-color 0.2s ease;
    background-color: $primary-light-grey;

    @include media-breakpoint-up(lg) {
        margin-top: 1.5rem;
        margin-bottom: 5rem;
    }

    // functionality
    [mx-collection-filter] {
        .dropdown-item {
            padding: 0;
            label {
                width: 100%;
            }
        }
    
        .badge-container {
            padding: 0;
            display: flex;
            flex-wrap: wrap;
            opacity: 0;
    
            .filter-badge {
                display: none;
                &[data-filter-active] {
                    display: block;
                }
            }
    
            .filter-badge[data-filter-active] ~ .clear-all {
                display: block;
            }
        }
    }

    .filter-ready .badge-container {
        opacity: 1;
    }
    
    [mx-filter-results] {
        opacity: 0;
        &.filter-ready {
            opacity: 1;
        }
    }

    // styling
    .filter-options {
        border-bottom: 1px solid $primary-blue;
        margin: 0;
    }

    .badge-row {
        max-height: 200px;
        margin: 0;
        // transition: all 0.2s ease;
    }

    .btn-filter {
        background: none;
        font-size: $font-size-body-2;
        font-family: $SaaSeries;
        color: $primary-blue;
        margin-right: 4rem;
        padding: 1.25rem .5rem;
        border-radius: 0;
        border-bottom: toRem(3.5) solid transparent;

        transition: border-bottom-color .3s;

        width: fit-content;

        &.show,
        &:hover {
            border-bottom-color: $primary-blue;
        }

        &::after {
            border-width: .4em;
            border-top-width: .5em;
            transform: translateY(toRem(2));
        }
    }

    .dropdown-menu {
        background: $primary-light-grey;
        box-shadow: 0 30px 60px #00000029;
        border-radius: 0;
        border: 0;
        min-width: 15rem;
        .dropdown-item {
            padding: .5rem 1.5rem;

            label {
                font-size: $font-size-body-2;
                font-family: $SaaSeries;
                font-weight: 400;
                color: $primary-blue;
                cursor: pointer;
            }

            &:active {
                background-color: var(--bs-dropdown-link-hover-bg);
            }
        }
    }
    .btn-group.dropdown:not(:has(li)){
        display: none;
    }

    .num-results {
        padding: 1.5rem toRem(8) toRem(6);
        @include media-breakpoint-up(md) {
            text-align: right;
        }
    }

    .badge-container {
        gap: .5rem;
        margin-top: 1rem;

        .filter-badge {
            margin-bottom: 0.75rem;
            @include media-breakpoint-up(md) {
                margin-bottom: 0.5rem;
            }

            .btn-clear-all-filters {
                padding: 0.75rem 1.25rem 0.575rem 0.25rem;   
            }
        }

        .btn-filter-badge {
            background: $secondary-white;
            color: $primary-blue;
            border-radius: 5rem;
            transition: .3s;
            font-size: toRem(14);
            font-family: $SaaSeries;
            display: flex;
            align-items: center;
            margin-right: 0.25rem;
            @include media-breakpoint-up(md) {
                background: $primary-blue;
                color: $secondary-white;
                padding: 0.65rem 1.25rem 0.575rem;
            }

            @media (hover:hover) {
                &:hover {
                    background: $secondary-gold;
                }
            }

            .icon-holder {
                width: 0.3rem;
                margin: 0 0.6rem;
                svg {
                    padding: 0.2rem;
                }
            }
        }

        .btn-clear-all-filters {
            @include header-5;
            color: $secondary-white;
            transition: all 0.3s;
            text-decoration: underline;
            padding-left: 0.25rem;
            @include media-breakpoint-up(md) {
                color: $primary-blue;
            }

            &:hover {
                color: $secondary-gold;
            }
        }
    }

    .sort-options {
        .dropdown-toggle {
            width: 100%;
            max-width: 100%;
            text-align: left;
            margin: 0;
            @include media-breakpoint-up(md) {
                text-align: right;
            }
        }
    }

    .mobile-filter-button {
        margin: 0;
    }

    .mobile-filter-list {
        padding: 0;
    }
    
    .product-category-filter {
        @include media-breakpoint-down(md) {
            margin-top: 3rem;
        }
    }

    input[type=checkbox]:checked {
        background: $secondary-white;
        &::after {
            border-bottom: 2px solid $primary-blue;
            border-left: 2px solid $primary-blue;
        }   

        @include media-breakpoint-up(lg) {
            background: $primary-blue;
            &::after {
                border-bottom: 1px solid $secondary-white;
                border-left: 1px solid $secondary-white;
            }   
        }
    }

    .noFish {
        display: none;
        @include media-breakpoint-up(lg) {
            padding: 1rem 0 4rem;
        }

        &.show {
            display: block;
        }

        img {
            width: 100%;
            max-width: 350px;
            height: auto;
            @include media-breakpoint-up(lg) {
                max-width: 450px;
            }
        }

        .tagline {
            font-size: 2rem;
            text-align: center;
            @include media-breakpoint-up(lg) {
                font-size: 3.25rem;
            }
        }
    }
}

