.image-text-panels {
    padding-top: 4rem;
    padding-bottom: 2rem;
    @include media-breakpoint-up(md) {
        padding-bottom: 2rem;
    }
    @include media-breakpoint-up(lg) {
        padding-top: 5rem;
    }

    &.theme--dark{
        background: $primary-blue;
        color: $secondary-white;
    }
    &.theme--light{
        background: $primary-light-grey;
        color: $primary-blue;
    }

    &.bg_white {
        background: $secondary-white;
        color: $primary-blue;

        .text-wrapper--description a {
            color: $secondary-gold;
        }
    }
    
    &__block {
        display: flex;
        flex-direction: column;
        margin-bottom: 3.75rem;
        
        .image-wrapper {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 70%;
            height: 100%;
            max-height: 150px;
            @include media-breakpoint-up(lg) {
                padding: 0 3rem;
                width: 100%;
                max-width: 350px;
            }
            
            picture {
                width: 100%;
                height: 100%;
            }
            
            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }

            &.align-left {
                padding-left: 0;
                
                img {
                    object-position: left center;
                }
            }
        }

        .text-wrapper {
            text-align: left;

            &--description {
                p {
                    font-size: 1.125rem;
                    margin-bottom: 0.125rem;
                }
                a {
                    color: $secondary-white;
                }
            }
        }
    }
}