.media-hero {
    position: relative;
    padding-bottom: 1rem;
    margin-top: -5rem;
    height: 103vh;
    overflow: hidden;
    @include media-breakpoint-up(md) {
        max-height: 950px;
        margin-top: -7rem;
    }
    @include media-breakpoint-up(xxl) {
        max-height: 1150px;
    }

    &__image, &__video {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 0;
        
        img, video {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }
    }

    &__video {
        z-index: 1;
    }

    .filter {
        background: linear-gradient(rgba(0, 0, 0, 0.7) 0%,rgba(0, 0, 0, 0) 70%);
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        @include media-breakpoint-up(lg) {
            background: linear-gradient(180deg,rgba(0, 0, 0, 0.7) 0%,rgba(0, 0, 0, 0) 20%);
        }
    }

    .content-wrap {
        display: flex;
        flex-direction: column;
        align-items: start;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 2;
        width: 100%;
        @include media-breakpoint-up(xl) {
            position: relative;
            align-items: start;
            top: inherit;
            left: inherit;
            transform: none;
            padding-top: 31vh;
        }
        @include media-breakpoint-up(xxl) {
            padding-top: 35vh;
        }
    }
    
    &__header {
        position: relative;
        padding: 0 1rem 0 0;
        z-index: 1;
        text-shadow: 5px 15px 40px rgba(8, 24, 47, 0.6);
    }

    &__subheader {
        padding: 0 1rem 0 0;
        text-shadow: 2px 3px 24px rgba(8, 24, 47, 1);
        p { padding-bottom: 0; }
    }

    &.has-columns {
        padding-bottom: 0;
        height: auto;
        
        @include media-breakpoint-up(md) {
            max-height: none;
        }
        @include media-breakpoint-up(xxl) {
            max-height: none;
        }
        
        .content-wrap {
            position: relative;
            transform: none;
            top: initial;
            left: initial;
            padding-top: 10rem;
            @include media-breakpoint-up(md) {
                min-height: 66vh;
                padding-top: 31vh;
            }
            @include media-breakpoint-up(xxl) {
                padding-top: 35vh;
            }
        }

        .columns-wrapper {
            margin-top: 7rem;
            position: relative;
            width: 100%;
            bottom: 0;
            z-index: 3;
            
            .row--columns {
                position: relative;
                padding-bottom: 3rem;
    
                &::before {
                    content: '';
                    width: 100vw;
                    height: 100%;
                    background: $primary-light-grey;
                    top: 0;
                    left: -100vw;
                    position: absolute;
                    z-index: 1;
                }
            }
            .media-hero__column {
                padding-top: 3.5rem;
                padding-right: 1rem;
                background: $primary-light-grey;
                position: relative;
                z-index: 1;
                @include media-breakpoint-up(md) {
                    padding-right: 3rem;
                }
                @include media-breakpoint-down(lg) {
                    &.single {
                        min-width: 260px;
                    }
                }
                // Fill background of empty columns when it wraps
                &::before {
                    content: '';
                    width: 100%;
                    height: calc(100% + 3rem);
                    background: $primary-light-grey;
                    top: 100%;
                    left: 0;
                    position: absolute;
                    z-index: 1;
                }
            }
            .media-hero__column-header {
                margin-bottom: 1.75rem;
            }
            .media-hero__column-body {
                margin-bottom: 0;

                p {
                    margin-bottom: 0.35rem;
                    line-height: 1.5;
                }
            }
    
        }
    }

    // Theme Colors 
    &.theme--light{
        .media-hero__subheader,
        .media-hero__header {
            color: $secondary-white;
        }
        &.has-columns {
            .row--columns {
                color: $primary-blue;
                &::before {
                    background: $primary-light-grey;
                }
            }
            .media-hero__column {
                background: $primary-light-grey;
                &::before {
                    background: $primary-light-grey;
                }
            }
        }
    }
    &.theme--dark{
        .media-hero__subheader,
        .media-hero__header{
            color: $primary-blue;
        }
        &.has-columns {
            .row--columns {
                color: $secondary-white;
                &::before {
                    background: $primary-blue;
                }
            }
            .media-hero__column {
                background: $primary-blue;
                &::before {
                    background: $primary-blue;
                }
            }
        }
    }

    // Text Alignment
    &.alignment--center {
        .media-hero__header {
            text-align: center;
            margin: 0 auto;
            @include media-breakpoint-up(lg) {
                text-align: left;
                margin: 0;
            }
        }
        .media-hero__subheader {
            text-align: center;
            margin: 0 auto;
            @include media-breakpoint-up(lg) {
                text-align: left;
                margin: 0;
            }
        }
        .content-wrap {
            justify-content: center;
            @include media-breakpoint-up(lg) {
                align-items: start;
            }
        }
        .media-hero__btn {
            margin: 0 auto;
            @include media-breakpoint-up(lg) {
                margin: 0;
            }
        }
    }
}