.card-basic {
    width: 270px;
    @include media-breakpoint-up(md) {
        width: auto;
    }

    a { 
        text-decoration: none; 
        color:$primary-blue;
    }

    h2, h3, h4, p {
        max-width: 270px;
        @include media-breakpoint-up(xxl) {
            max-width: 330px;
        }
    }

    &__header {
        @include header-3;
        text-decoration: underline rgba($secondary-gold,  0);
        transition: text-decoration-color 300ms;
        margin-top: 1rem;
        margin-bottom: 0.25rem;
        @include media-breakpoint-up(lg) {
            margin-top: 1.125rem;
        }
    }

    &__sub-header {
        @include header-5;
    }

    &__sub-header_2 {
        @include header-5;
        margin-top: 0.5rem;
    }

    &__body {
        margin-top: 0.75rem;
        @include body-1;
    }
    &:has( a ){
        &:hover{
            h2 {
                text-decoration: underline rgba($secondary-gold,  1);
            }
        }
    }

    .image-wrap {
        height: auto;
        width: 270px;
        height: 270px;
        position: relative;
        overflow: hidden;
        // background: $secondary-white;
        // cursor: pointer;
        @include media-breakpoint-up(xxl) {
            height: 330px;       
            width: 330px;
        }
        
        .filter {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            transition: opacity 0.3s;
            background: $primary-blue;
            z-index: 1;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
            position: relative;  
        }

        .product-image {
            // object-fit: contain;
        }

        .product-image-hover {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            object-fit: cover;
            transition: opacity 0.3s;
            background-color: white;
        }
        
        &:hover {
            .product-image-hover {
                opacity: 1;
            }
        }
    }

    &:has( a ){
        @include media-breakpoint-up(lg) {
            &:hover .image-wrap .filter {
                opacity: 0.5;
            }
        }
    }

    .button-container {
        margin-bottom: 3.5rem;
    }
    &.width-auto{
        width: auto;
        // height: auto;
        max-width: 100%;
        h1, h2, h3, .h3, p, .image-wrap{
            width: auto;
            // height: auto;
            max-width: 100%;                
        }
    }
}

.theme--dark{
    .card-basic {
        a{
            color:$white;
        }
        &__header {
            text-decoration: underline rgba($white,  0);
            transition: text-decoration-color 300ms;
        }
        :has( a ){
            &:hover{
                .card-basic__header{
                    text-decoration: underline rgba($white,  1);
                }
            }
        }
    }
}